import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/loan-account/account.service';
import { UrlService } from 'app/views/home/url.service';
import { EsignPopComponent } from '../esign-pop/esign-pop.component';
import { UploadSignOrEsignComponent } from '../upload-sign-or-esign/upload-sign-or-esign.component';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import {eSign_URL} from 'app/shared/services/api.service'

@Component({
  selector: 'app-digital-sign-in-now-or-later',
  templateUrl: './digital-sign-in-now-or-later.component.html',
  styleUrls: ['./digital-sign-in-now-or-later.component.scss']
})
export class DigitalSignInNowOrLaterComponent implements OnInit {
  accountId: any;
  accountdetailsForStatusUpdate: Object;
  previousUrl: string = '';
  show: boolean=false;
  show1: boolean = false;
  loanAccountId: any;
  loanaccountID: any;
  loanInfo: any;
  isDone: boolean = false;
  constructor(private dialog: MatDialog,
    private router: Router,
    private ls: LocalStoreService,
    private accountService: AccountService,
    private apiService: ApplicationEntryStageService,
    private dialogService: DialogService,
    private urlService: UrlService,
    private api: ApiService) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    this.show1 = this.ls.getItem('eSignStage') && (this.ls.getItem('signedImage') !== null)
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.accountId = this.ls.getItem('accountId');

    this.urlService.previousUrl$
      .subscribe((previousUrl: string) =>{
        this.previousUrl = previousUrl
      });
  }

  openPopUp(data?: any) {
    let response:any;
    if(data === undefined ||data === null) {
      response = {
        isSignLater: false,
        screen: 'Create Account'
      };
    } 
    if(!this.show) {
      let dialogRef: MatDialogRef<any> = this.dialog.open(UploadSignOrEsignComponent, {
        width: '720px',
        disableClose: true,
        data: ((data === undefined ||data === null) ? response : data)
      })
      dialogRef.afterClosed().subscribe(res => {
        this.loanAccountId =  this.loanaccountID;
        console.log("dismiss res",res);
        if(res){
          this.show=this.ls.getItem("enable");
          // this.show = this.ls.getItem('eSignStage')
          this.show1=this.ls.getItem("enable");

          console.log(this.show);
          this.getByAccountId(this.loanAccountId);
        }
      })
    }

} 
 getByAccountId(loanAccountId: any) {
  this.api.fetchByLoanAccountId(loanAccountId)
  .subscribe(resp => {
    this.accountdetailsForStatusUpdate = resp;
        this.updateAccountStatus(resp);
  });
  }
  updateAccountStatus(accData) {
    accData.status = 'BACKLOG';
    this.api.updateLoanDetailsStatus(accData)
    .subscribe(updateResp => {
      console.log("updateResp:: ", updateResp);
    });
  }
  onDone(){
    this.isDone=true;
    this.dialogService.alertForLoanFinishButton().subscribe((response) => {
      console.log("response ::", response);                 
    });    

  }
  goBack() {
    this.router.navigate([this.previousUrl]);
  }

  resetState() {
    this.ls.removeItem('LoanEntryStage');
    this.ls.removeItem('LOAN_DETAIL_DONE');
    this.ls.removeItem('ASSET_DETAIL_DONE');
    this.ls.removeItem('VEHICLE_DETAIL_DONE');
    this.ls.removeItem('ADMISSION_DETAIL_DONE');
    this.ls.removeItem('CUSTOMERINFO_DETAIL_DONE');
    this.ls.removeItem('MANDATE_DETAIL_DONE');
    this.ls.removeItem('FINANCIAL_DETAIL_DONE');
    this.ls.removeItem('COLLATERAL_DETAIL_DONE');
    this.ls.removeItem('GUARANTOR_DETAIL_DONE');

    this.ls.removeItem('LOAN_ENRICHMENT_DONE');
    this.ls.removeItem('LOAN_INTERST_DETAIL_DONE');
    this.ls.removeItem('LOAN_DISBURSEMENT_DETAIL_DONE');
    this.ls.removeItem('LOAN_REPAYMENT_DETAIL_DONE');
    this.ls.removeItem('LOAN_CHARGE_DETAILS');
    this.ls.removeItem('LOAN_ACCOUNT_SERVICES_DONE');
    this.ls.removeItem('LOAN_ENRICHMENT_DONE');

    this.ls.removeItem('assessment-details');
    this.ls.removeItem('offer-acceptReject-details');
    this.ls.removeItem('loan-summary-details');
    this.ls.removeItem('assessmentScreen');
    this.ls.removeItem('OfferIssueStageDone');

    this.ls.removeItem('OfferIssue');
    this.ls.removeItem('OfferIssueRejectStageScreen');
    this.ls.removeItem('OfferIssueRejectScreen');

    this.ls.removeItem('underWritingStage');
    this.ls.removeItem('CREDIT_RATING_DONE');
    this.ls.removeItem('VALUATION_OF_ASSET_DONE');
    this.ls.removeItem('LEGAL_OPINION_DONE');
    this.ls.removeItem('legalScreen');
    this.ls.removeItem('valuationAsset');
    this.ls.removeItem('CreditRatingDetails');

    this.ls.removeItem('LOAN_QUALITATIVE_SCOREBOARD_DONE');
    this.ls.removeItem('LOAN_ASSESSMENT_DETAILS_DONE');
    this.ls.removeItem('LOAN_ASSESSMENT_STAGE_DONE');

    this.ls.removeItem('SUPERVISOR_APPROVAL_STAGE');
    this.ls.removeItem('isapprovedSupervisor');
    this.ls.removeItem('APPROVED_DETAILS');
    this.ls.removeItem('PostOfferAmmendementStageScreen');
    this.ls.removeItem('PostOfferAmmendementScreen');
    this.ls.removeItem('post-offer-amend-stage');

    this.ls.removeItem('ACCOUNT_CREATTION_STAGE_DONE');
    this.ls.removeItem('ACCOUNT_CREATE_DETAILS_DONE');
    this.ls.removeItem('eSignStage');
    this.ls.removeItem('loanAccountId');
    this.ls.removeItem('enable');
    this.ls.removeItem('primaryCustomerName');
    this.ls.removeItem('cardownership_value');
    this.ls.removeItem('cId');
    this.ls.removeItem('signedImage');
    this.ls.removeItem('LoanNoOfApplicants');
    this.ls.removeItem('isHomeLoan');
    this.ls.removeItem('productName');
    this.ls.removeItem('signedImage')

    sessionStorage.clear();
    this.accountService.refresh.next();
    this.router.navigate(['/loan']);
  }

  onSubmit() {
    console.log(this.loanaccountID);
    
    let payload = {
      loanAccountId:this.loanaccountID,
      subject:"Link to sign later",
      esignLink: eSign_URL,
    };
    this.api.signInLater(payload)
      .subscribe((data: any) => {
        console.log("data::",data);
        this.getByAccountId(this.loanaccountID);
        this.ls.setItem('eSignStage', true);
        this.ls.setItem('enable',true)
        this.accountService.refresh.next();
        let response: any = {
          email: data.email,
          customerId: data.customerId,
          subject: data.subject,
          isSignLater: true,
          screen: 'Loan Account'
        };
        this.isDone=true;
    this.dialogService.alertForLoanSignLater().subscribe((response) => {
      console.log("response ::", response);                 
    });  
    this.show = this.ls.setItem("enable",true);
        
      }, (err: any) => {


      });
    }
  }

