import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-underwriting-stage',
  templateUrl: './underwriting-stage.component.html',
  styleUrls: ['./underwriting-stage.component.scss']
})
export class UnderwritingStageComponent implements OnInit {
 // PArent 3
 isUnderWritingStageDone: boolean = false;
 // SUB TREES
 isCreditRatingDetails: boolean = false;
 isValuationofAsset: boolean = false;
 isLegalOpinion: boolean = false;
  loanAccId: any;
  constructor(private ls: LocalStoreService,private dialogService : DialogService,private router: Router) { 
    this.loanAccId = "200";
  }

  ngOnInit(): void {
     // Parent 1
     this.isUnderWritingStageDone = this.ls.getItem('underWritingStage');
        // Parent 1 childs nav
    this.isCreditRatingDetails = this.ls.getItem('CREDIT_RATING_DONE');
    this.isValuationofAsset = this.ls.getItem('VALUATION_OF_ASSET_DONE');
    this.isLegalOpinion = this.ls.getItem('LEGAL_OPINION_DONE');

  }

  goToCreditRatingSummary(){
    this.dialogService.creditRatingSummaryDialog();
  }

  goToValuationAssetsSummary(){
    this.dialogService.valuationAssetsSummaryDialog();
  }

  goToLegalOpinionSummary(){
    this.dialogService.legalOpinionSummaryDialog();
  }
  viewSummary(){
    this.router.navigate([`loan/account/list/underwritingStage/summary`,]);
  }

}
