import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';


@Component({
  selector: 'app-offer-accept-reject-summary',
  templateUrl: './offer-accept-reject-summary.component.html',
  styleUrls: ['./offer-accept-reject-summary.component.scss']
})
export class OfferAcceptRejectSumaryComponent implements OnInit {

  userRecommendation={'userRecommendation':'Accept'};
  // offerDetails = {'applicantName'         :'ABC',
  //                'approvedLoanAmount'     :'$200,000',
  //                'loanTenure'             :'5 years',
  //                'rateOfIntrest'          :'5.9',
  //                'loanAmountRecomended'   :'$500,000',
  //                'installmentType'        :'EMI',
  //                'charge'                 :'5 years',
  //                'installmentAmount'      :'$100,000',
  //                'installmentFreq'        :'Monthly',
  //                'principle'              :'$100,000',
  //                'intrest'                :'$100,000',    
  //                'date'                   :'12/2/2021',
  //                   };
  offerDetails: any;
  constructor(private router: Router,
    public dialogRef: MatDialogRef<OfferAcceptRejectSumaryComponent>,
    private apiService: ApiService,
    private dataService:DataService,
    ) { }

  ngOnInit(): void {
    this.fetchOfferAcceptRejectDetails();
  }

  fetchOfferAcceptRejectDetails() {
      this.apiService.getofferDetails(7654)
        .subscribe((data : any)=> {
          this.offerDetails = data;
          console.log(this.offerDetails);
        })
    }

  edit(){
    this.dataService.sendNavParam(this.offerDetails);
    this.dialogRef.close();
    this.router.navigate(['loan/account-approval/offer-acceptReject-details']);
    
  }
  close(){
    this.dialogRef.close();
    this.router.navigate([`/loan/account-approval/list`]);
  }
}
