import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common'
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from '../../account.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
@Component({
  selector: 'app-loan-disbursement-details',
  templateUrl: './loan-disbursement-details.component.html',
  styleUrls: ['./loan-disbursement-details.component.scss']
})
export class LoanDisbursementDetailsComponent implements OnInit {

  maxDate = new Date();
  minDate = new Date();
  loanDisbursementForm: FormGroup;
  internalAccount: FormGroup;
  resp: any;
cifNumber: any;
 accountNoArr = [];
  showEditOption: boolean = false;
  loanaccountId: any;
  disbursementDetails: any;
  loanInfo: any;
  constructor(
    private loader: AppLoaderService,
    private ls: LocalStoreService,
    public datepipe: DatePipe,private router:Router,private formBuilder: FormBuilder,private api: ApiService,
    private snack: MatSnackBar,private accountService:AccountService) { }

  ngOnInit(): void {
    this.loanaccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountId = this.loanInfo.loanAccountId;
    }
    let isLoanDisbursementDetails = this.ls.getItem('LOAN_DISBURSEMENT_DETAIL_DONE');
    // setTimeout(() => {
     
    // }, 10); 
    if (this.loanaccountId != null && isLoanDisbursementDetails) {
      this.showEditOption = true;
      this.getDisbursementDetails();
    } else {
      this.showEditOption = false;
      this.buildformGroup();
    }  
    this.fetchByCif(); 
  }

  getDisbursementDetails(){
    this.loader.open();
      this.api.loanDisbursementFetchById(this.loanaccountId).subscribe(disbursementDetails=>{
        this.loader.close();
        this.disbursementDetails = disbursementDetails;
        this.buildformGroup(this.disbursementDetails);
    }, (err: any) => {
      this.loader.close();
    }); 
  }


  buildformGroup(item?) {
    const lsLoanAmount = this.ls.getItem('loanAmount') || '';

    // let latest_date= new Date(item.firstDisbursementDate).toISOString().substring(0, 10);
     this.loanDisbursementForm = this.formBuilder.group({
      mutipleDisbursementRequired: [item?item.mutipleDisbursementRequired:true],
      loanAccountId:this.loanaccountId,
      loanDisbursementId:[item?item.loanDisbursementId:''],
      numberOfDisbursement:[item?item.numberOfDisbursement:'',Validators.required],
      totalDisbursement:[item?item.totalDisbursement:'',Validators.required],
      loanAmount:[item?item.loanAmount:lsLoanAmount,Validators.required],
      firstDisbursementDate:[item?this.datepipe.transform(item.firstDisbursementDate, 'yyyy-MM-dd'):'',Validators.required],
      disbursementMode:[item?item.disbursementMode:'',Validators.required],
    });
    this.internalAccount = this.formBuilder.group({
      customerAccount:[item?item.customerAccount:'',Validators.required],
      branchCode:[item?item.branchCode:'',Validators.required],
  
     
    });

  }
submit(){

  let latest_date =this.datepipe.transform(this.loanDisbursementForm.value.firstDisbursementDate, 'yyyy-MMM-dd');
  

  this.loanDisbursementForm.value.branchCode=this.internalAccount.value.branchCode;
  this.loanDisbursementForm.value.customerAccount=this.internalAccount.value.customerAccount;
  this.loanDisbursementForm.value.firstDisbursementDate=latest_date;
  this.api.loanDisbursementSave(this.loanDisbursementForm.value).subscribe(resp => {
    this.snack.open('Loan Disbursement Details Saved!', 'OK', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
    this.next();
  
  }, err => {
    this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
  })
  
}

_keyPress(event: any) {
  const pattern = /[0-9]/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
}

next(){
  this.ls.setItem('LOAN_DISBURSEMENT_DETAIL_DONE', true);
  this.accountService.refresh.next();
  this.router.navigate([`/loan/account/application-enrichment/loan-repayment`]);
}
goBack() {
 
  this.router.navigate([
    `/loan/account/application-enrichment/intrest-details`,
  ]);
}
fetchByCif(){
    this.cifNumber=this.ls.getItem('cId');
    this.api.fetchAccountIdByCustId(this.cifNumber).subscribe((accountNo: [])=>{
      this.accountNoArr = accountNo;         
      this.internalAccount.get('customerAccount').setValue(this.accountNoArr?.[0]);      
      console.log(this.internalAccount.get('customerAccount').value);
      this.fetchByValue();
    }     
      );  
          
  }
  fetchByValue(){
    console.log(this.internalAccount.get('customerAccount').value);
    
    this.api.fetchByBranchCode(this.internalAccount.get('customerAccount').value).subscribe((resp=>{
      console.log(resp);
      this.internalAccount.get('branchCode').setValue(resp.branchCode);
      
    }));
    
  }
  keyPressAlphanumeric(event) {
    var inp = String.fromCharCode(event.keyCode);
    if (/^[a-zA-Z0-9_ ]*$/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
}