import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from '../../account.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
@Component({
  selector: 'app-offer-accept-reject',
  templateUrl: './offer-accept-reject.component.html',
  styleUrls: ['./offer-accept-reject.component.scss']
})
export class AcceptOfferAcceptRejectComponent implements OnInit {

  generateArr:string[]=['With Schedule','without Schedule'];
  approvedLoan: number;
  disableDone:boolean =true;
  offeracceptForm: FormGroup;
   showEditOption: boolean = false;
  isAssesmentDetailsScreen: boolean = false;
  isOfferIssueScreen: boolean = false; 
  isOfferIssueRejectScreen: boolean  = false;
  arraySystemRecommendation:any[]=['Approved','Rejected'];
  loanaccountID: any;
  loanInfo: any;
  selectedDay: number = 1;
 // day: number;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  //year: number;
  years: any[] = [];
  installmentTypes: string[] = ['EMI'];
  cifNumber: any;
  accountDetails: any;
  
  constructor(private router: Router,private ls: 
    LocalStoreService,private apiService:ApiService, private snack: MatSnackBar,private loader: AppLoaderService,
    private formBuilder: FormBuilder,private appApiService: ApplicationEntryStageService,private accountService:AccountService) { 
    this.offeracceptForm = this.formBuilder.group({
      //approvedLoan : ['',[Validators.required]],
        approvedLoanAmount : ['',[Validators.required]],
        applicantName:['',[Validators.required]],
        loanTenure:[''],
        installmentType:['',[Validators.required]],
        installmentFrequency:[''],    
        rateOfInterest:[], 
        customerResponse:[''], 
        offerIssueDate:['',[Validators.required]],
        generateOffer:['', [Validators.required]],
        offerExpiryDate:['',[Validators.required]],
        dateOfOfferAcceptOrReject:['',[Validators.required]],
        year:['',[Validators.required]],
      month:['',[Validators.required]],
      day:['',[Validators.required]],
      
    
    })
  }

  ngOnInit(): void {
    this.selectedYear = new Date().getFullYear();
  
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }

    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }

    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    let isOfferIssueRejectScreen: boolean = JSON.parse(this.ls.getItem('OfferIssueRejectScreen'));
     setTimeout(() => {
        if (this.loanaccountID != null && isOfferIssueRejectScreen) {
          this.showEditOption = true;
          
          this.getAccountDetails();
        } 
        else {
          console.log("else");
          this.showEditOption = false;
          this.buildOfferAcceptForm();
        }
      }, 10);
    this.fetchByCif();
    this.loanData();
    this.intrestData();
    this.installmentTypeData();
  }
  getAccountDetails() {
    //this.loader.open();
    this.apiService.getOfferAcceptReject(this.loanaccountID).subscribe((accountDetails) => {
     // this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildOfferAcceptForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }
   buildOfferAcceptForm(data?) {
    data.offerIssueDate = data ? moment(data.offerIssueDate).format() : '';
    data.generateOffer = data ? moment(data.generateOffer).format():'';
    data.offerExpiryDate = data ? moment(data.offerExpiryDate).format():'';
    data.dateOfOfferAcceptOrReject = data ? moment(data.offerAcceptOrRejectDate).format():'';
    this.offeracceptForm = this.formBuilder.group({
      approvedLoanAmount : [data ? data.approvedLoanAmount:''],
      loanTenure:[data?data.loanTenure:''],
      applicantName:[data ? data.applicantName:''],
      installmentFrequency:[data ? data.installmentFrequency:''],    
      rateOfInterest:[data ? data.rateOfInterest:''],  
      offerIssueDate:[data ? data.offerIssueDate:''],
      generateOffer:[data ? data.generateOffer:''],
      offerExpiryDate:[data ? data.offerExpiryDate:''],
      year:[data ? data.loanTenure.split("-")[0]:''],
      month:[data ? data.loanTenure.split("-")[1]:''],
      day:[data ? data.loanTenure.split("-")[2]:''],
      installmentType: [data ? data.installmentType: ''],
      dateOfOfferAcceptOrReject:[data? data.dateOfOfferAcceptOrReject:''],
      customerResponse:[data ? data.customerResponse:''],
    })

  }

  //offeracceptForm()
goBack(){
  this.router.navigate(["/loan/account/offerstage/list"]);
}
Submit(){
  let details:  any;
    //details.push(this.legalOpinion.value);
    let now = new Date();
    now=this.offeracceptForm.value.offerIssueDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    let now1 = new Date();
    now1=this.offeracceptForm.value.offerAcceptOrRejectDate;
    var dateStringWithTime1 = moment(now1).format('YYYY-MMM-DD HH:MM:SS');
    console.log("OAR", dateStringWithTime1)
    let now2 = new Date();
    now2=this.offeracceptForm.value.offerExpiryDate;
    var dateStringWithTime2 = moment(now2).format('YYYY-MMM-DD HH:MM:SS');
    console.log("OE", dateStringWithTime2)
    let now3 = new Date();
    now3=this.offeracceptForm.value.generateOffer;
    var dateStringWithTime3 = moment(now3).format('YYYY-MMM-DD HH:MM:SS');
    console.log("GO", dateStringWithTime3)
    var aprloan=this.offeracceptForm.value.approvedLoanAmount;
    aprloan= parseInt(aprloan);
    var rate=this.offeracceptForm.value.rateOfInterest;
    rate= parseInt(rate);
     const renewData =
    this.offeracceptForm.value.year +
    "-" +
    this.offeracceptForm.value.month +
    "-" +
    this.offeracceptForm.value.day;
  this.offeracceptForm.get("loanTenure").setValue(renewData);
  delete this.offeracceptForm.value.year;
  delete this.offeracceptForm.value.month;
  delete this.offeracceptForm.value.day;
  this.offeracceptForm.value.interest =parseInt(this.offeracceptForm.value.interest);
    this.offeracceptForm.value.installmentAmount =parseInt(this.offeracceptForm.value.installmentAmount);
    let payload={
      loanAccountId: this.loanaccountID,
      approvedLoanAmount:aprloan,
      applicantName:this.offeracceptForm.value.applicantName,
      loanTenure:renewData,
      installmentType:this.offeracceptForm.value.installmentType,
      installmentFrequency:this.offeracceptForm.value.installmentFrequency,
      rateOfInterest:rate,
      offerIssueDate:dateStringWithTime,
      interest: this.offeracceptForm.value.interest,
      installmentAmount: this.offeracceptForm.value.installmentAmount,
      dateOfOfferAcceptOrReject:dateStringWithTime1,
      offerExpiryDate: dateStringWithTime2,
      generateOffer: dateStringWithTime3,
      customerResponse:this.offeracceptForm.value.customerResponse,
    }
    console.log("payload",payload)
    this.apiService.offerDetails(payload).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Offer Issue Accept/Reject Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
     
      }
      this.Next();
    })
    
  
}
Next(){
  this.ls.setItem('OfferIssueRejectScreen', true);
  this.ls.setItem('OfferIssueRejectStageScreen', true);
  this.accountService.refresh.next();
  this.router.navigate(["/loan/account/post-offer-amend-stage/list"]);

}
_keyPress1(event: any) {
  const pattern = /^[0-9]*\.?[0-9]*$/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
  const input = event.target.value;
  if (input.length === 0 && event.which === 48) {
    event.preventDefault();
  }
}

_keyPress(event: any) {
  const pattern = /[0-9]/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
}
onInput(event: any, flag) {
  if (flag) {
    this.approvedLoan = event.target.value;
  } else {
    this.approvedLoan = event;
  }

  console.log("this.salary)::", this.approvedLoan);
  let sal = this.approvedLoan.toString().replace(/,/g, '').split(".");
  sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  let final = sal.join(".");
  console.log("final", final);
  this.offeracceptForm.controls.approvedLoan.setValue(final);
}
letterOnly(event) {
  var charCode = event.keyCode;
          if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
              return true;
          else
              return false;
}

fetchByCif(){
  this.cifNumber=this.ls.getItem('cId');
  this.appApiService.fetchByCif(this.cifNumber).subscribe((res: any) => {
  this.offeracceptForm.get('applicantName').setValue(res.customerInfoList[0].firstName +" "+ res.customerInfoList[0].lastName);
  }, (err: any) => {
    console.error(err);
  })
}
loanData(){
  console.log(this.loanaccountID);
  this.apiService.fetchByLoanAccountId(this.loanaccountID).subscribe((res:any)=>{   
    console.log(res); 
    this.offeracceptForm.get('approvedLoanAmount').setValue(res.approvedLoanAmount); 
    console.log(res.loanTenure);       
    let dataYear = res.loanTenure.split("-")[0];
    console.log(dataYear);
    let dataMonth = res.loanTenure.split("-")[1];
    console.log(dataMonth);
    let dataDay = res.loanTenure.split("-")[2];
    console.log(dataDay);    
    this.offeracceptForm.get('year').setValue(dataYear);
    this.offeracceptForm.get('month').setValue(dataMonth);
    this.offeracceptForm.get('day').setValue(dataDay);
  
  }, (err: any) => {
    console.error(err);
  })
}
intrestData(){
  console.log(this.loanaccountID);
  this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((res:any)=>{    
    this.offeracceptForm.get('rateOfInterest').setValue(res.rateOfInterest);
  })
}
installmentTypeData(){
  console.log(this.loanaccountID);
  this.apiService.getingOfferIssueDetails(this.loanaccountID).subscribe((res:any)=>{
    console.log(res);
     this.offeracceptForm.get('installmentType').setValue(res.installmentType);
     this.offeracceptForm.get('installmentFrequency').setValue(res.installmentFrequency);
     let issueDate =  moment(res.offerIssueDate).format();
     this.offeracceptForm.get('offerIssueDate').setValue(issueDate);
     console.log(issueDate);    
  });
}

}
