import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';

@Component({
  selector: 'app-legal-opinion',
  templateUrl: './legal-opinion.component.html',
  styleUrls: ['./legal-opinion.component.scss']
})
export class LegalOpinionComponent implements OnInit {
  legalOpinionId:any;
  showEditOption: boolean = false;
  lawyerArr:string[]=['John Asset','Peter','Henery Nicholus','Mountbaton'];
  descriptionArr:string[]=['Title Deeds clear','sanction','Asset-backed securitisations'];
  assetArea: number;
  legalOpinion: FormGroup;
  accountDetails: any;
  loanInfo: any;
  loanAccountId: any;

  constructor(private formBuilder: FormBuilder,private apiService:ApiService, private snack: MatSnackBar,private ls: 
    LocalStoreService,private loader: AppLoaderService,
    private router: Router) {
     
        this.legalOpinion= this.formBuilder.group({

          assetAreaSize: ['',[Validators.required]],
          description:[''],
          lawyerName:[''],
          mktValOfAsset: ['',[Validators.required]],
          opinion:[''],
          opinionDate:[''],
          valuationDate:[''],
          
        })
     }

  ngOnInit() {
    
    let isLegalOpinionScreen: boolean = JSON.parse(this.ls.getItem('legalScreen'));
    //if (this.legalOpinionId != null && isLegalOpinionScreen) {
      if (55655 != null && isLegalOpinionScreen) {
      this.showEditOption = true;
      this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
      if(this.loanInfo){
        this.loanAccountId = this.loanInfo.loanAccountId;
      }
      setTimeout(() => {
        this.getLegalOpinionDetails();
      }, 10);
     
    }
    else {
      console.log("else");
      this.showEditOption = false;
      this.buildOfferIssueForm();
    }
   
  }
  getLegalOpinionDetails(){
    this.loader.open();
    this.apiService.getLegalOpinion(112).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildOfferIssueForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }
  buildOfferIssueForm(data?){
    this.legalOpinion = this.formBuilder.group({
      assetAreaSize : [data ? data.assetAreaSize:''],
      description:[data ? data.description:''],
      lawyerName:[data ? data.lawyerName:''],
      mktValOfAsset:[data ? data.mktValOfAsset:''],
      opinion:[data ? data.opinion:''],    
      opinionDate:[data ? data.opinionDate:''],  
      valuationDate:[data ? data.valuationDate:''],
      
    
    })

  }
  onclick(){
    let now = new Date();
    now=this.legalOpinion.value.opinionDate;
    var dateStringWithTime = moment(now).format('YYYY-MM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
  }

  Submit(){
    let details:  any;
    //details.push(this.legalOpinion.value);
    let now = new Date();
    now=this.legalOpinion.value.opinionDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
    var pk=this.legalOpinion.value.assetAreaSize;
    pk= parseInt(pk);
    console.log("pk",pk);
    
    //console.log("addbranch::",details);
    let payload={
      
      //assetAreaSize:this.legalOpinion.value.assetAreaSize,
      assetAreaInUnits:this.legalOpinion.value.assetAreaInUnits,
      assetAreaSize:pk,
      description:this.legalOpinion.value.description,
      isDecisionFavorable:"yes",
      lawyerName:this.legalOpinion.value.lawyerName,
      loanAccountId:112,
      mktValOfAsset:this.legalOpinion.value.mktValOfAsset,
      opinion:this.legalOpinion.value.opinion,
      opinionDate:dateStringWithTime,
      valuationDate:dateStringWithTime
      
      
      //valuationDate:this.legalOpinion.value.valuationDate,
      
      
      /*bankCode: this.holidaypopupForm.value.bankCode,
      branchCode: this.holidaypopupForm.value.branchCode,
      applicationDate:this.holidaypopupForm.value.applicationDate,
      year:year,
      id:this.holidaypopupForm.value.id,
      month:month,
     // calendar:calendar
     calendar:date.toString()*/

    }
    this.apiService.UpdateLegalOpinion(payload).subscribe(response => {
      console.log(response);
      if (response === 200 || response !== null) {
        this.snack.open('  Legal Opinion  Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.Next();
      }
      else if (response === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }, err => {
      console.log(err);
    });
    
  }
  Next(){
    this.ls.setItem('legalScreen', true);
    this.router.navigate(["/loan/account/list/offer-issue/offer-assessment"]);
  
  }
  
  goBack(){
    this.router.navigate(["/loan/account/list/offer-issue/valuation-asset"]);
  }
  _keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  onInput(event: any, flag) {
    if (flag) {
      this.assetArea = event.target.value;
    } else {
      this.assetArea = event;
    }
  
    console.log("this.salary)::", this.assetArea);
    let sal = this.assetArea.toString().replace(/,/g, '').split(".");
    sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = sal.join(".");
    console.log("final", final);
    this.legalOpinion.controls.mktValOfAsset.setValue(final);
  }
  onInput1(event: any, flag) {
    if (flag) {
      this.assetArea = event.target.value;
    } else {
      this.assetArea = event;
    }
  
    console.log("this.salary)::", this.assetArea);
    let sal = this.assetArea.toString().replace(/,/g, '').split(".");
    sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = sal.join(".");
    console.log("final", final);
    this.legalOpinion.controls.assetAreaSize.setValue(final);
  }
}