import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';


@Component({
  selector: 'app-asset-details',
  templateUrl: './asset-details.component.html',
  styleUrls: ['./asset-details.component.scss']
})
export class AssetDetailsComponent implements OnInit {
  assetForm:FormGroup;
  arrayCountry: any[] = [];
  arrayState: any[] = [];
  arrayCity: any[] = [];
  entityName: string;
  assetDetails:any
  showEditOption: boolean = false;
  loanaccountId: any;
  loanInfo: any;
  mortgagedBranch: any;
  arrayAccountBranch: any;  

  constructor(private fb:FormBuilder,
    private loader: AppLoaderService,private apiService:ApiService, 
    private snack: MatSnackBar,private ls: LocalStoreService,private router:Router,
    private accountService: AccountService ) { }

  ngOnInit(): void {
    this.loanaccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    this.getAccountBranchDetails();
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountId = this.loanInfo.loanAccountId;
    }
    console.log("this.loanaccountId",this.loanaccountId);
    this.buildformGroup();
    this.getCountryName("country");
    this.getAllAuthCities();
    this.getStateName("state");
    let isAssetDetailscreen: boolean = JSON.parse(this.ls.getItem('ASSET_DETAIL_DONE'));
    //if (this.loanAccountId != null && isAssetDetailscreen) {
      if (this.loanaccountId != null && isAssetDetailscreen) {
        this.showEditOption = true;
        
        this.getAssetDetails();
      } 
      else {
        console.log("else");
        this.showEditOption = false;
        this.buildformGroup();
      }
      // setTimeout(() => {
        
      // }, 10);

  }
  getAssetDetails(){
    this.loader.open();
    //this.apiService.getAssetValuation(this.loacnAccountId).subscribe((accountDetails) => {
    this.apiService.getAssetails(this.loanaccountId).subscribe((assetDetails) => {
      this.loader.close();
      this.assetDetails = assetDetails;
      this.getStateName(this.assetDetails.country);
      // this.getCityName(this.assetDetails.state);
      console.log(this.assetDetails);
      this.buildformGroup(this.assetDetails);
    }, (err: any) => {
      this.loader.close();
    });

    
  }
  getCountryName(key: string) {
    this.apiService.getAllCountries().subscribe((data: any) => {
     for(let i=0; i<data.length;i++){
       if(data[i].authStatus==='A'){     
         this.arrayCountry.push(data[i].countryName);
         this.arrayCountry.sort();
       }
     }
   });
 }
 getStateName(country,isAdd?) {
      if(isAdd){
        this.assetForm.get('zipCode').setValue('');
        this.assetForm.get('state').setValue('');
        this.assetForm.get('city').setValue('');
}
  this.apiService.getAllCountries().subscribe((data: any) => {
    for(let i=0; i<data.length;i++){       
      if(data[i].countryName == country){
        this.apiService.getStateByCountryId(data[i].id).subscribe((resp: any) => {
        this.arrayState = resp
        })
        break;   
      }
    }
  })
}
  // getCityName(state: any) {
  //   this.entityName = "city";
  //   console.log(state)
  //   this.apiService.getDropDownValues1(this.entityName, state).subscribe((data) => {
  //     this.arrayCity = data;
  //     console.log("-->", this.arrayCity)
  //     // this.applicant.valueChanges.subscribe(data=>{
  //     //   console.log('data :: before ',data.city)
  //     //   data.city='';
  //     //   console.log('data :: after ',data.city)
  //     // })
  //     // this.applicant.value.city='';
  //     // this.applicant.get('city').reset();
  
  //   });
  // }

  getAllAuthCities(){
    this.apiService.getAuthorizedCityInfo().subscribe(res => res.map(r => this.arrayCity.push(r.cityName)))
  }
  onStateChange(){
    this.assetForm.get('zipCode').setValue('');
    this.assetForm.get('city').setValue('');
  }
  onCityChange(){
    this.assetForm.get('zipCode').setValue('');
  }

getAccountBranchDetails() {
    //debugger
    this.apiService.getAccountBranch().subscribe((data) => {
      console.log("data:: AccountBranch ", data)
      this.arrayAccountBranch = data;
    });
  }
  buildformGroup(data?){
    console.log("data",data);
    this.assetForm = this.fb.group({
      id:[''],
      loanaccountId : this.loanaccountId,
      mortgagedBranch:[data ? data.mortgagedBranch:'',Validators.required],
      dimensions:[data ? data.dimensions:''],
      assetStatus:[data ? data.assetStatus:''],
      homeType:[data ? data.homeType:''],
      marketValue:[data ? data.marketValue:'',Validators.required],
      building:[data ? data.building:'',Validators.required],
      street: [data ? data.street:'', Validators.required],
      locality: [data ? data.locality:'', Validators.required],
      city: [data ? data.city:'', Validators.required],
      state: [data ? data.state:'', Validators.required],
      country: [data ? data.country:'', Validators.required],
      zipCode: [data ? data.zipCode:'',[Validators.required, Validators.pattern("^((\\+91-?)|0)?[0-9]{5,10}$")]],

    })
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  
  goBack(){
    this.router.navigate([`/loan/account/loan-details`]);
  }

  next(){
    this.ls.setItem('ASSET_DETAIL_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/loan/account/customer-information`]);
  }
  submitForm(){
    
    
    
    let payload={
      loanAccountId:this.loanaccountId,
      mortgagedBranch:this.assetForm.value.mortgagedBranch,
      street:this.assetForm.value.street,
      locality:this.assetForm.value.locality,
      building:this.assetForm.value.building,
      marketValue:this.assetForm.value.marketValue,
      homeType:this.assetForm.value.homeType,
      dimensions:this.assetForm.value.dimensions,
      city:this.assetForm.value.city,
      assetStatus:this.assetForm.value.assetStatus,
      state:this.assetForm.value.state,
      
      country:this.assetForm.value.country,
      zipCode:this.assetForm.value.zipCode,
    }
    console.log("payload",payload)
    this.apiService.upsertAssetDetails(payload).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Asset Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
     
      }
      
      
      
    })
    
  this.next();
}

getCityandStateByZipcode(countryName, zipcode) {
  if (countryName && zipcode.length >= 6) {
    this.apiService.fetchStateCityByZipcode(countryName, zipcode).subscribe((res: any) => {
      if(res){
        this.assetForm.get('state').setValue(res.stateName);
        this.assetForm.get('city').setValue(res.cityName);
      }
      else{
        this.snack.open("Entered Zipcode is not exists", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
      }
    })
  }
}

}
