
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-credit-rating-details',
  templateUrl: './credit-rating-details.component.html',
  styleUrls: ['./credit-rating-details.component.scss']
})
export class CreditRatingDetailsComponent implements OnInit {
  creditDetails: FormGroup;
  agencyRating:  any[] = [];
 accountId: any;
 agencyId:any;
  agencyArr:any[]=[ 'riyan', 'john daey','joo'];
  customerArr:any[]=[ 'Jo Smith', 'David','Chris'];
  rating: any;
  loanAccountId:any;
  showEditOption: boolean = false;
  accountDetails: any;
  loanInfo: any;

  constructor(private formBuilder: FormBuilder,private loader: AppLoaderService,
    private apiService:ApiService, private snack: MatSnackBar, 
    private ls: 
    LocalStoreService,
    private router: Router) {
      this.creditDetails = this.formBuilder.group({
        
        agencyName: ['',[Validators.required]],
        ratings: ['',[Validators.required]],
        remarks: ['',[Validators.required]],
        
      })
     }

  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem('loanAccountId');
    console.log("this.accountId ",this.accountId);
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    //this.productaccountID = this.ls.getItem('accountId');
    let isCreditRateScreen: boolean = JSON.parse(this.ls.getItem('CreditRatingDetails'));
    //if (this.loanAccountId != null && isCreditRateScreen) {
      setTimeout(() => {
        if (this.loanAccountId != null && isCreditRateScreen) {
          this.showEditOption = true;
         // this.cifNumberPrimary = this.ls.getItem("CIF_NUM_PRIMARY");
          //this.isCifValid = true;
          this.getAccountDetails();
        } else {
          console.log("else");
          this.showEditOption = false;
          this.buildAccountForm();
        }
      }, 10);
 
  }
  getAccountDetails(){
    this.loader.open();
    //this.apiService.getCreditRating(this.loanAccountId).subscribe((accountDetails) => {
      this.apiService.getCreditRating(this.loanAccountId).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildAccountForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }
  buildAccountForm(data?){
    this.creditDetails = this.formBuilder.group({
      
      customerName: [data ? this.ls.getItem('NAME') : ''],
      agencyName: [data ? data.agencyName : '', Validators.required],
      ratings: [data ? data.ratings : '', Validators.required],
     remarks: [data ? data.remarks : '', Validators.required],
      
    });

  }

  submit(){
    this.next();
  }
  next(){
    //let details:  any[] = [];
    this.agencyRating.push(this.creditDetails.value);
    console.log("addbranch::",this.agencyRating);
    let payload={
      agencyRating: this.agencyRating,
      loanAccountId:this.loanAccountId
      //loanAccountId:this.loanAccountId

    }
    this.apiService.UpdateCreditRating(payload).subscribe(response => {
      console.log(response);
      if (response === 200 || response !== null) {
        this.snack.open('  Credit Rating  Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.ls.setItem('CreditRatingDetails', true);
        //this.accountService.refresh.next();
        this.router.navigate(["/loan/account/list/offer-issue/valuation-asset"]);
      }
      else if (response === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }, err => {
      console.log(err);
    });
    console.log("payload",payload);

  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
}
