import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';
import * as moment from 'moment';
import { date } from 'ngx-custom-validators/src/app/date/validator';
import { DatePipe } from '@angular/common'
@Component({
  selector: 'app-legal-opinion',
  templateUrl: './legal-opinion.component.html',
  styleUrls: ['./legal-opinion.component.scss']
})
export class UnderWrittingLegalOpinionComponent implements OnInit {
  maxDate = new Date();
  minDate = new Date();
  accountForm: FormGroup;
  isCreditRatingDetails: any;
  isValuationofAsset: any;
  isLegalOpinion: any;
  loanaccountID: any;
  showEditOption: boolean = false;
  isChecked = true;
  disabled = true;
  disabledToggle = true;
  loanInfo: any;
  opinionData: any;
  constructor(private formBuilder: FormBuilder, private apiService: ApiService,public datepipe: DatePipe,
    private loader: AppLoaderService,private snack: MatSnackBar,private api: ApiService,
    private ls: LocalStoreService, private router: Router,private accountService:AccountService ) {
      this.accountForm= this.formBuilder.group({
        assetAreaSize: ['',[Validators.required]],
        description:['',[Validators.pattern(".*[a-zA-Z].*")]],
        lawyerName:['',[Validators.required,Validators.pattern(".*[a-zA-Z].*")]],
        mktValOfAsset: ['',[Validators.required]],
        opinion:['',[Validators.required]],
        isDecisionFavorable:[''],
        opinionDate:['',Validators.compose([ Validators.required, Validators.min(this.opinionData)])],
        valuationDate:['',Validators.compose([ Validators.required, Validators.min(this.opinionData)])],
        assetAreaInUnits:['',[Validators.required]],
      })
     }

  ngOnInit(): void {
       
    //this.isLegalOpinion = this.ls.getItem('LEGAL_OPINION_DONE');
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    console.log("this.productaccountID1",this.loanaccountID);
    let isVehicleDetailsScreen: boolean = JSON.parse(this.ls.getItem('LEGAL_OPINION_DONE'));
    //if (this.loanAccountId != null && isVehicleDetailsScreen) {
      setTimeout(() => {
        if (this.loanaccountID!= null && isVehicleDetailsScreen) {
          this.showEditOption = true; 
          this.gettingLegalOpinionDetail();
        } 
        else {
          console.log("else");
          this.showEditOption = false;
          this.buildAccountForm();
        }
      }, 10);
    this.valueData();
    this.lastUpdateByCif();
    //this.buildAccountForm();
/*if(this.isLegalOpinion==false){
  this.buildAccountForm();
  }
else{
  this.gettingLegalOpinionDetail();
}*/
   
  
  }
  gettingLegalOpinionDetail() {
    this.loader.open();
    this.api.gettingLegalOpinionDetails(this.loanaccountID)
      .subscribe((resp: any) => {
        if(resp.opinion=="Favourable"){
          this.isChecked=true;
        }
        else{
          this.isChecked=false;
        }
        this.loader.close();
       console.log(resp);
       
      this.buildAccountForm(resp,true);;
    }, (err: any) => {
      this.loader.close();
      
    });
   
  }
  toggle(event){
    // let value = event.target.value;
     console.log("value",event);
     if(event=="Not Favourable"){
       this.disabled = false;
       this.isChecked = false;
       this.disabledToggle = false;
 
     }
     else if(event=="Favourable"){
       this.disabled = true;
       this.isChecked = true;
       this.disabledToggle = true;
     }
     else{
       this.isChecked = false;
     }

     
 
   }
  buildAccountForm(data?,Response?:boolean) {
    data.opinionDate = data ? moment(data.opinionDate).format() : '';
    data.valuationDate = data ? moment(data.valuationDate).format() : '';
    console.log(data);
    this.accountForm = this.formBuilder.group({
      assetAreaInUnits: [data ? data.assetAreaInUnits:''],
      assetAreaSize:[data ? data.assetAreaSize:0],
      description:[data ? data.description:''],
      isDecisionFavorable:[this.isChecked],
      lawyerName:[data ? data.lawyerName:''],
      //legalOpinionId:[data ? data.legalOpinionId:0,Validators.required],
      loanAccountId:[this.loanaccountID],
      mktValOfAsset:[data ? data.mktValOfAsset:''],
      opinion:[data ? data.opinion:''],
      opinionDate:[data ? data.opinionDate:''],
      valuationDate:[data ? data.valuationDate:''],
    });
    console.log("this.accountForm",this.accountForm);
  
  }
 // OnSubmit(val){
  OnSubmit(){
    //console.log(val,"Values")
    // val.valuationDate= moment(val.opinionDate).format('YYYY-MMM-DD HH:mm:ss');
   //val.opinionDate= moment(val.opinionDate).format('YYYY-MMM-DD HH:mm:ss');
  // val.valuationDate= moment(val.valuationDate).format('YYYY-MMM-DD HH:mm:ss');
    // this.ls.setItem('CREDIT_RATING_DONE', true);
    let now = new Date();
    now=this.accountForm.value.opinionDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    let now1 = new Date();
    now1=this.accountForm.value.valuationDate;
    var dateStringWithTime1 = moment(now1).format('YYYY-MMM-DD HH:MM:SS');
    var pk=this.accountForm.value.assetAreaSize;
    pk= parseInt(pk);
    console.log("pk",pk);
    let payload={
      assetAreaInUnits:this.accountForm.value.assetAreaInUnits,
      assetAreaSize:pk,
      description:this.accountForm.value.description,
      isDecisionFavorable:this.accountForm.value.isDecisionFavorable,
      lawyerName:this.accountForm.value.lawyerName,
      loanAccountId:this.loanaccountID,
      mktValOfAsset:this.accountForm.value.mktValOfAsset,
      opinion:this.accountForm.value.opinion,
      opinionDate:dateStringWithTime,
      valuationDate:dateStringWithTime1

    }
    this.api.saveLegalOpinion(payload).subscribe((data: any) => {
      console.log(data);
      if (data === 200 || data !== null) {
        this.snack.open(' Legal Opinion Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.ls.setItem('LEGAL_OPINION_DONE', true);
        this.isCreditRatingDetails = this.ls.getItem('CREDIT_RATING_DONE');
        this.isValuationofAsset = this.ls.getItem('VALUATION_OF_ASSET_DONE');
        this.isLegalOpinion = this.ls.getItem('LEGAL_OPINION_DONE');

        if(this.isCreditRatingDetails == true && this.isValuationofAsset== true && this.isLegalOpinion ==true){
           this.ls.setItem('underWritingStage', true);
           this.next();
        }
      }
      else if (data === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }, err => {
      console.log(err);
    });

   this.router.navigate([`/loan/account/assessment/list`]);
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  _keyPress1(e){
    let val = e.target.value
    if ((e.which === 32 && !val.length) || (e.keyCode >= 48 && e.keyCode <= 57) ) {
    e.preventDefault();
}
  }
next(){
  this.accountService.refresh.next();
  this.router.navigate([`/loan/account/assessment/list`]);
}
goBack(){
   this.router.navigate([
      `/loan/account/list/underwritingStage/valuationofassets`,
    ]);
}
valueData(){ 
    this.api.gettingValuationDetails(this.loanaccountID).subscribe(res=>{
      console.log(res);
      
      this.accountForm.get('mktValOfAsset').setValue(res.mktValOfAsset);
    }     
      );  
}
lastUpdateByCif(){
  this.apiService.getLoanDetails(this.loanaccountID).subscribe((resp:any)=>{
   
   this.opinionData = this.datepipe.transform(resp.applicationDate,"yyyy-MM-dd");
 console.log(this.opinionData);
 
  }, (err: any) => {
    console.error(err);
  })
}
}
