import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from '../../account.service';

@Component({
  selector: 'app-credit-rating-details',
  templateUrl: './credit-rating-details.component.html',
  styleUrls: ['./credit-rating-details.component.scss']
})
export class UnderWrittingCreditRatingDetailsComponent implements OnInit {
  creditForms: FormGroup;
  customerNames: any[]=[];

  isCreditRatingDetails: any;
  loanaccountID: any;
  customerName: any;
  loanInfo: any;
  // customerNames=['Gayathri','joSmith'];
  constructor(private formBuilder: FormBuilder,private ls: LocalStoreService,private appApiService: ApplicationEntryStageService,
     private snack: MatSnackBar,private api: ApiService , private router: Router,private accountService:AccountService) { }

  ngOnInit(): void {
    this.customerName=this.ls.getItem('primaryCustomerName');
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getCustomerInformationDetails();
    }, 10);

this.isCreditRatingDetails = this.ls.getItem('CREDIT_RATING_DONE');
if(this.isCreditRatingDetails==false){
  this.buildCollateralForm();
}
else{
  this.gettingCreditRatingDetail();
}
    // this.buildCollateralForm();
  console.log(this.isCreditRatingDetails);
  }

  gettingCreditRatingDetail() {
    this.api.gettingCreditRatingDetails(this.loanaccountID)
      .subscribe((resp: any) => {
       console.log(resp);
       this.buildCollateralForm(resp,true);
      });
  }
 
  // buildCollateralForm(data?, isResponse?: boolean) {
  buildCollateralForm(data?,isResponse?:boolean) {
    console.log(data);
 
    this.creditForms = this.formBuilder.group({
      customerName:[this.customerName,Validators.required],
      loanAccountId:this.loanaccountID,
      agencyRating: this.formBuilder.array([this.addModFormGroup(data)])
    });
    console.log(this.creditForms);
  }
  getCustomerInformationDetails() {
    this.appApiService.fetchCustomersByLoanAccId(this.loanaccountID,'loan')
      .subscribe((resp: any) => {
     console.log("resp for customer information names ",resp.data);
     for (let index = 0; index < resp.data.length; index++) {
       if(resp.data[index].middleName != null){
      this.customerNames[index] = resp.data[index].firstName +" " +resp.data[index].middleName +" "+ resp.data[index].lastName;
       }
       else{
        this.customerNames[index] = resp.data[index].firstName +" "+ resp.data[index].lastName;
       }
     }
     console.log("customer name ",this.customerNames)
     if(this.customerNames.length === 1){
      this.creditForms.get('customerName').setValue(this.customerNames[0])
     }
      }, (err: any) => {
      });
  }
    // convenience getters for easy access to form fields
    get f() { return this.creditForms.controls; }
    get c() { return this.f.agencyRating as FormArray; }
 
  addFormGroup() {
    this.c.push(this.formBuilder.group({
      agencyId:[0],
      agencyName: ['',Validators.required],
      ratings: ['',Validators.required],
      remarks: [''],

    }));
  }
  

  addModFormGroup(data): FormGroup {
    console.log(data,"inside addModeFormGrp");
    return this.formBuilder.group({
      agencyId:[data?data.agencyId : 0],
      agencyName: [data?data.agencyRating[0].agencyName : '',Validators.required],
      ratings: [data?data.agencyRating[0].ratings : '',Validators.required],
      remarks: [data?data.agencyRating[0].remarks : ''],

    })
  }
  OnSubmit(val){
    console.log("val",val);
    val.accountType ="loan";
    // this.ls.setItem('CREDIT_RATING_DONE', true);
    this.api.saveCreditDetails(val).subscribe((data: any) => {
      console.log(data);
      if (data === 200 || data !== null) {
        this.snack.open(' Credit Score Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.ls.setItem('CREDIT_RATING_DONE', true);
        this.accountService.refresh.next();
        this.router.navigate([`/loan/account/list/underwritingStage/valuationofassets`]);
      }
      else if (data === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }, err => {
      console.log(err);
    });
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  goBack() {
    this.router.navigate(["/loan/account/list/underwritingStage"]);
  }

}
