import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-account-creation-stage',
  templateUrl: './account-creation-stage.component.html',
  styleUrls: ['./account-creation-stage.component.scss']
})
export class AccountCreationStageComponent implements OnInit {
   // PArent 
 isAccountCreationStageDone: boolean = false;
 // SUB TREES
 isAccountCreateDetails: boolean = false;
 isOfferAcceptReject: boolean = false;

  constructor(private router: Router,private ls: LocalStoreService,private dialogService : DialogService,public dialog: MatDialog) { }

  ngOnInit(): void {
    // Parent 1
    this.isAccountCreationStageDone = this.ls.getItem('accountCreationStage');
    // Parent 1 childs nav
this.isOfferAcceptReject = this.ls.getItem('OFFER_ACCEPT_REJECT_DONE');
this.isAccountCreateDetails = this.ls.getItem('ACCOUNT_CREATE_DETAILS_DONE');
  }
  addDetails(){
    this.router.navigate([`/loan/account/account-creation-stage/offer-accept-reject`]);

  }
  addDetails1(){
    this.router.navigate([`/loan/account/account-creation-stage/account-create-details`]);
    
  }
  goToOfferAcceptRejectSummary(){
    this.dialogService.OfferAcceptRejectSummaryDialog();
  }

  goToAccountCreateSummary(){
    this.dialogService.AccountCreateSummaryDialog();
  }
  



}
