import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';

@Component({
  selector: 'app-offer-issue',
  templateUrl: './offer-issue.component.html',
  styleUrls: ['./offer-issue.component.scss']
})
export class OfferIssueComponent implements OnInit {

  generateArr:string[]=['With Schedule','without Schedule'];
  approvedLoan: number;
  accountDetails: any;
  OfferIssueForm: FormGroup;
  showEditOption: boolean = false;
  disableDone:boolean =true;
  isAssesmentDetailsScreen: boolean = false;
  isOfferIssueScreen: boolean = false; 
  isOfferIssueRejectScreen: boolean  = false;
  loanInfo: any;
  loanaccountID: any;


  constructor(private router: Router,private ls: 
    LocalStoreService,private apiService:ApiService, private snack: MatSnackBar,private loader: AppLoaderService,
    private formBuilder: FormBuilder) { 
    this.OfferIssueForm = this.formBuilder.group({
      approvedLoanAmount : [],
      applicantName:[''],
      loanTenure:[],
      installmentType:[''],
      installmentFrequency:[''],    
      rateOfInterest:[],  
      offerIssueDate:[''],
      generateOffer:[''],
    
    })
  }

  ngOnInit(): void {
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    let isOfferIssueScreen: boolean = JSON.parse(this.ls.getItem('OfferIssue'));
    setTimeout(() => {
      if (44444 != null && isOfferIssueScreen) {
        this.showEditOption = true;
        
        this.getAccountDetails();
      } 
      else {
        console.log("else");
        this.showEditOption = false;
        this.buildOfferIssueForm();
      }
    }, 10);
   
    //this.getAccountType("account_type");
  }
  getAccountDetails() {
    this.loader.open();
    this.apiService.getOfferIssueDetails(this.loanaccountID?this.loanaccountID:44444).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildOfferIssueForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }
  buildOfferIssueForm(data?) {
    this.OfferIssueForm = this.formBuilder.group({
      approvedLoanAmount : [data ? data.approvedLoanAmount:''],
      applicantName:[data ? data.applicantName:''],
      loanTenure:[data ? data.loanTenure:''],
      installmentType:[data ? data.installmentType:''],
      installmentFrequency:[data ? data.installmentFrequency:''],    
      rateOfInterest:[data ? data.rateOfInterest:''],  
      offerIssueDate:[data ? data.offerIssueDate:''],
      generateOffer:[data ? data.generateOffer:''],
    
    })

  }
  goBack(){
    this.router.navigate(["/loan/account/offerstage/offer-assessment"]);
  }
Submit(){
  let details:  any;
    //details.push(this.legalOpinion.value);
    let now = new Date();
    now=this.OfferIssueForm.value.opinionDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
    var aprloan=this.OfferIssueForm.value.approvedLoanAmount;
    aprloan= parseInt(aprloan);
    var rate=this.OfferIssueForm.value.rateOfInterest;
    rate= parseInt(rate);
    let payload={
      loanAccountId:parseInt("44444"),
      approvedLoanAmount:aprloan,
      applicantName:this.OfferIssueForm.value.applicantName,
      loanTenure:this.OfferIssueForm.value.loanTenure,
      installmentType:this.OfferIssueForm.value.installmentType,
      installmentFrequency:this.OfferIssueForm.value.installmentFrequency,
      rateOfInterest:rate,
      offerIssueDate:dateStringWithTime,
      generateOffer:this.OfferIssueForm.value.generateOffer,
    }
    console.log("payload",payload)
    this.apiService.upsertOfferIssue(payload).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
     
      }
    })
    
  this.Next();
}
Next(){
  this.ls.setItem('OfferIssue', true);
  this.router.navigate(["/loan/account/offerstage/offer-accept-reject"]);

}

_keyPress1(event: any) {
  const pattern = /^[0-9]*\.?[0-9]*$/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
  const input = event.target.value;
  if (input.length === 0 && event.which === 48) {
    event.preventDefault();
  }
}

_keyPress(event: any) {
  const pattern = /[0-9]/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
}
onInput(event: any, flag) {
  if (flag) {
    this.approvedLoan = event.target.value;
  } else {
    this.approvedLoan = event;
  }

  console.log("this.salary)::", this.approvedLoan);
  let sal = this.approvedLoan.toString().replace(/,/g, '').split(".");
  sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  let final = sal.join(".");
  console.log("final", final);
  this.OfferIssueForm.controls.approvedLoanAmount.setValue(final);
}
}