import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-loan-repayment',
  templateUrl: './loan-repayment.component.html',
  styleUrls: ['./loan-repayment.component.scss']
})
export class LoanRepaymentComponent implements OnInit {
  loanRepaymentForm:FormGroup;

  constructor(private fb:FormBuilder) { }

  ngOnInit(): void {
    this.buildformGroup();
  }

  buildformGroup(){
    this.loanRepaymentForm = this.fb.group({
      id:[''],
      type_of_repayment:[''],
      first_repayment_date:[''],
      maturity_date:[''],
      loan_tenure:[''],
      repayment_mode:[''],
      repayment_frequency:[''],
      customer_account:[''],
      moratorium_period:[''],
      branch_code:['']
    })
  }

}
