import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-digital-signing',
  templateUrl: './digital-signing.component.html',
  styleUrls: ['./digital-signing.component.scss']
})
export class DigitalSigningComponent implements OnInit {

  informations = [
    {
      'id':1,
      'title': '1. Digital Signing',
      'content':"Customer can either sign now or sign later",
      'percentage':70,
      'status':'Pending',
      'state': 'application/digital-sign/options',
      'avatar':"assets/images/esign.jpg"

    }
  ];

  accountId:any;
  isEsignScreen:boolean = false;
  accountService: any;
  constructor(private router:Router,private ls: LocalStoreService,private cdr:ChangeDetectorRef) { }

  ngOnInit(){
    this.accountId = this.ls.getItem('accountId');
    this.isEsignScreen = this.ls.getItem('eSignStage');

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      this.isEsignScreen = this.ls.getItem('eSignStage');
    
      this.cdr.markForCheck();
    });
  }

  goToDetail(item){
    console.log(item);
    this.router.navigate([`/home/${item.state}`]);
  }

}
