
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';

@Component({
     selector: 'app-offer-accept-reject-stage',
    templateUrl: './offer-accept-reject-stage.component.html',
     styleUrls: ['./offer-accept-reject-stage.component.scss']
   })
export class OfferAcceptRejectStageComponent implements OnInit {
  isQualitativeScreen:boolean = false;
  isReportScreen:boolean = false;
  accountId: any;
  isAssessmentStageDone: boolean = false;
  assessmentReportSummary:any;
  isChargesScreen: boolean = false;
  overDraftByStatus:boolean;
  isAssesmentDetailsScreen: boolean = false;
  isOfferIssueScreen: boolean = false; 
  isOfferIssueRejectScreen: boolean  = false;
  
  constructor(private router: Router, private dialogService: DialogService, 
    private ls: LocalStoreService, private cdr: ChangeDetectorRef,
     private accountService: AccountService, private apiService: ApiService) { }

  // ngOnInit(): void {
    
  // }
  ngOnInit() {
    this.accountId = this.ls.getItem('accountId');
    let overdraftStatus = this.ls.getItem('overDraftByStatus') || null;
    console.log("fetch",overdraftStatus);
    this.overDraftByStatus = overdraftStatus != null ? overdraftStatus.isActive : false; 
    console.log(this.overDraftByStatus);
    this.isAssesmentDetailsScreen = this.ls.getItem('assessmentScreen');
    this.isOfferIssueScreen = this.ls.getItem('OfferIssue');
    this.isOfferIssueRejectScreen = this.ls.getItem('OfferIssueRejectScreen');
   

    this.accountService.refresh.subscribe((res: any) => {
      this.accountId = this.ls.getItem('accountId');
      let overdraftStatus = this.ls.getItem('overDraftByStatus') || null;
      this.overDraftByStatus = overdraftStatus != null ? overdraftStatus.isActive : false; 
      this.isAssessmentStageDone = this.ls.getItem('assessmentStage');
      this.isQualitativeScreen = this.ls.getItem('ASSESSMENT_QA_DONE');
      this.isAssesmentDetailsScreen = this.ls.getItem('assessmentScreen');
     this.isOfferIssueScreen = this.ls.getItem('OfferIssue');
     this.isOfferIssueRejectScreen = this.ls.getItem('OfferIssueRejectScreen');
     
      this.cdr.markForCheck();
    });
    this.getData(this.accountId);
  }

  getData(id){
    this.apiService.getApplnAssessmentDetails(id).subscribe((response) => {
      this.assessmentReportSummary = response;
      console.log( this.assessmentReportSummary);
    });
  }

  popupAssessment(){
    this.dialogService.offerAssessmentSummaryDialog();
  }

  popup(){
    this.dialogService.offerIssueSummaryDialog();
  }

  popupOfferIssue(){
    this.dialogService.offerAcceptRejectSummaryDialog();
  }


}

