import { DatePipe } from "@angular/common";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { RepaymentScheduleComponent } from "./repayment-schedule/repayment-schedule.component";
import { AccountService } from "../../account.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import * as moment from 'moment';
// import { Year } from "@syncfusion/ej2-schedule";
@Component({
  selector: "app-loan-repayment-component",
  templateUrl: "./loan-repayment-component.component.html",
  styleUrls: ["./loan-repayment-component.component.scss"],
})
export class LoanRepaymentComponentComponent implements OnInit {
  maxDate = new Date();
  minDate = new Date();
  loanRepaymentForm: FormGroup;
  internalAccount: FormGroup;
  scheduleLink: FormGroup;
  selectedDay: number = 1;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  years: any[] = [];
  // day: number;
   year: string[];
  month:string[];
 //  day: string[];
  loanaccountId: any;
  data: any;
  fetchRescheduleData: any;
  showEditOption: boolean;
  repaymentDetails: any;
  date: void;
  scheduleDate: any;
  loanInfo: any;
  cifNumber: any;
 accountNoArr = [];
 loanTenure:string;
  fRepayment: any;
  repaymentFreq: any;
  loan_repayment_frequency: any;
  constructor(
    private formBuilder: FormBuilder,
    public datepipe: DatePipe,
    private snack: MatSnackBar,
    private api: ApiService,
    private apiService:ApiService,
    private router: Router,
    private ls: LocalStoreService,
    public dialog: MatDialog,
    private loader: AppLoaderService,
    private accountService: AccountService
  ) {}

  ngOnInit(): void {
    this.loanaccountId = this.ls.getItem("loanAccountId");
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountId = this.loanInfo.loanAccountId;
    }
    this.selectedYear = new Date().getFullYear();    
    for (let year = 0; year <= 50; year++) {
      this.years.push(year + " year");
    }

    for (let month = 0; month <= 11; month++) {
      this.months.push(month + " month");
    }

    for (let day = 0; day <= 30; day++) {
      this.days.push(day + " day");
    }
    
    // this.months = [
    //   "Jan",
    //   "Feb",
    //   "Mar",
    //   "Apr",
    //   "May",
    //   "Jun",
    //   "Jul",
    //   "Aug",
    //   "Sep",
    //   "Oct",
    //   "Nov",
    //   "Dec",
    // ];
    this.getRepaymentFreqDetails('loan_repayment_frequency');
    let isLoanDisbursementDetails = this.ls.getItem(
      "LOAN_REPAYMENT_DETAIL_DONE"
    );
    // setTimeout(() => {
    // }, 10);
    if (this.loanaccountId != null && isLoanDisbursementDetails) {
      this.showEditOption = true;
      this.getRepaymenttDetails();
    } else {
      this.showEditOption = false;
      this.buildAccountForm();
    }
  this.fetchByCif();
  this.loanData();
  this.lastUpdateByCif();
  }

  getRepaymentFreqDetails(loan_repayment_frequency){
    this.api.getRepaymentFreq(loan_repayment_frequency).subscribe(
      (resp) => {
        this.repaymentFreq = resp;
        console.log(this.repaymentFreq);
    });
  }

  getRepaymenttDetails() {
    //this.loader.open();
    this.api.loanRepaymentFetchById(this.loanaccountId).subscribe(
      (repaymentDetails) => {
       // this.loader.close();
        this.repaymentDetails = repaymentDetails;
        this.buildAccountForm(this.repaymentDetails);
      },
      (err: any) => {
        this.loader.close();
      }
    );
  }

  buildAccountForm(item?) {
    this.loanRepaymentForm = this.formBuilder.group({
      branchCode: ["",Validators.required],
      customerAccount: ["",Validators.required],
      bankName: ["",Validators.required],
      firstRepaymentDate: [
        item
          ? (this.datepipe.transform(item.firstRepaymentDate, 'yyyy-MM-dd'))
          : "",
          Validators.compose([ Validators.required, Validators.min(this.fRepayment)])
      ],
      loanAccountId: this.loanaccountId,
      loanRepaymentId: [item ? item.loanRepaymentId : ""],
      loanTenure: [item ? item.loanTenure : ""],
      maturityDate: [
        item ? new Date(item.maturityDate).toISOString().substring(0, 10) : "",
        Validators.required,
      ],
      moraturioumPeriod: [
        item ? item.moraturioumPeriod : "",
        Validators.required,
      ],
      repaymentFrequency: [
        item ? item.repaymentFrequency : "",
        Validators.required,
      ],
      repaymentMode: [item ? item.repaymentMode : "", Validators.required],
      typeOfRepayment: [item ? item.typeOfRepayment : "", Validators.required],
      day: [item ? item.loanTenure.split("-")[2] : "",  [Validators.required]],
      month: [item ? item.loanTenure.split("-")[1] : "",  [Validators.required]],
      year: [item ? item.loanTenure.split("-")[0] : "", [Validators.required]],
    });
   console.log(this.loanRepaymentForm.value);
    this.scheduleLink = this.formBuilder.group({
      firstRepaymentDate: this.loanRepaymentForm.get("firstRepaymentDate"),
      loanAccountId: this.loanaccountId,
    });

    this.internalAccount = this.formBuilder.group({
      branchCode: [item ? item.branchCode : "", [Validators.required,Validators.pattern(".*[a-zA-Z0-9].*")]],
      customerAccount: [item ? item.customerAccount : "", [Validators.required,Validators.pattern(".*[0-9].*")]],
      bankName: [item ? item.bankName : "", [Validators.required,Validators.pattern(".*[a-zA-Z].*")]],
    });
  }
getDays(year1,month1,day1){
  
var yr=moment(new Date()).add(parseInt(year1.split(" ")[0]), 'months').format('LLL');
var month=moment(yr).add(parseInt(month1.split(" ")[0]), 'year').format('LLL');
var finalDate=moment(month).add(parseInt(day1.split(" ")[0]), 'days').format('LLL');
console.log("finalDate--->",finalDate);
this.loanRepaymentForm.get("maturityDate").setValue(new Date(finalDate));
}

  openPopup() {
    console.log(this.loanRepaymentForm.value);
    console.log(this.internalAccount.value);
    console.log("payload", this.scheduleLink.value);
    let latest_date = moment(this.scheduleLink.value).format("YYYY-MMM-DD");

    console.log(latest_date);
    let payload = {
      firstRepaymentDate: latest_date,
      loanAccountId: this.loanaccountId,
    };

    console.log("payload", payload);
    this.api.scheduleLink(payload).subscribe((resp) => {
      this.fetchRescheduleData = resp;
      console.log(this.fetchRescheduleData);

      this.loanaccountId = this.ls.getItem("loanAccountId");
      localStorage.setItem(
        "fetchRescheduleData",
        JSON.stringify(this.fetchRescheduleData)
      );
      console.log(this.fetchRescheduleData);

      const dialogRef = this.dialog.open(RepaymentScheduleComponent, {
        width: "55%",
        backdropClass: "dialog-bg-trans",
        height: "55%",
        disableClose: true,
      });

      dialogRef.afterClosed().subscribe((result) => {
        console.log("The dialog was closed", result);
      });
    });

    if (latest_date != null) {
      latest_date = moment(this.scheduleLink.value).format("YYYY-MMM-DD");
    }
  }

  submit() {
    
    let latest_date =this.datepipe.transform(this.loanRepaymentForm.value.firstRepaymentDate, 'yyyy-MMM-dd');
    let latest_dateMaturity = this.datepipe.transform(
      this.loanRepaymentForm.value.maturityDate,
      "yyyy-MMM-dd"
    );
    const renewData =
      this.loanRepaymentForm.value.year +
      "-" +
      this.loanRepaymentForm.value.month +
      "-" +
      this.loanRepaymentForm.value.day;
   // this.loanRepaymentForm.get("loanTenure").setValue(renewData);
    this.loanRepaymentForm.value.loanTenure=renewData;
    this.loanRepaymentForm.value.firstRepaymentDate=latest_date;
    this.loanRepaymentForm.value.maturityDate=latest_dateMaturity;
    this.loanRepaymentForm.value.branchCode=this.internalAccount.value.branchCode;
    this.loanRepaymentForm.value.customerAccount=this.internalAccount.value.customerAccount;
    this.loanRepaymentForm.value.bankName=this.internalAccount.value.bankName;
   // this.loanRepaymentForm.get("bankName").setValue(this.internalAccount.value.bankName);
    //   this.loanRepaymentForm.get("firstRepaymentDate").setValue(latest_date);
    //   this.loanRepaymentForm.get("loanTenure").setValue(renewData);
    //   this.loanRepaymentForm.get("maturityDate").setValue(latest_dateMaturity);
    
    // delete this.loanRepaymentForm.value.year;
    // delete this.loanRepaymentForm.value.month;
    // delete this.loanRepaymentForm.value.day;

    console.log(this.loanRepaymentForm.value);
    this.api.loanRepaymentSave(this.loanRepaymentForm.value).subscribe(
      (resp) => {
        this.snack.open("Loan Repayment Details Saved!", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.next();
      },
      (err) => {
        this.snack.open(
          "Not able to connect to server!",
          "INTERNAL SERVER ERROR",
          {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          }
        );
      }
    );
  }
  goBack() {
    this.router.navigate([
      `/loan/account/application-enrichment/loan-disbursement`,
    ]);
  }

  convertDate(value) {
    let date = moment(value).format("YYYY-MMM-DD");
    console.log("date", date);
    if (date) return date;
  }

  next() {
    this.ls.setItem("LOAN_REPAYMENT_DETAIL_DONE", true);
    this.accountService.refresh.next();
    this.router.navigate([
      `/loan/account/application-enrichment/charge-details`,
    ]);
  }
  _keyPress(e) {   
    let val = e.target.value
    if (e.which === 32 && !val.length) {
    e.preventDefault();
}
  }
  letterOnly(event) {
    var charCode = event.keyCode;
            if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
                return true;
            else
                return false;
  }
  fetchByCif(){
    this.cifNumber=this.ls.getItem('cId');
    this.api.fetchAccountIdByCustId(this.cifNumber).subscribe((accountNo: [])=>{
      this.accountNoArr = accountNo;
      console.log(this.accountNoArr);                 
      this.internalAccount.get('customerAccount').setValue(this.accountNoArr?.[0]);
      this.fetchByValue();
    }     
      );       
  }
  fetchByValue(){
    console.log(this.internalAccount.get('customerAccount').value);
    
    this.api.fetchByBranchCode(this.internalAccount.get('customerAccount').value).subscribe((resp=>{
      console.log(resp);
      this.internalAccount.get('branchCode').setValue(resp.branchCode);
      this.internalAccount.get('bankName').setValue(resp.bankName);
      
    }));
    
  }
  loanData(){ 
  this.apiService.fetchByLoanAccountId(this.loanaccountId).subscribe((res:any)=>{      
    let dataYear = res.loanTenure.split("-")[0];    
    let dataMonth = res.loanTenure.split("-")[1];   
    let dataDay = res.loanTenure.split("-")[2];      
    this.loanRepaymentForm.get("year").setValue(dataYear);        
    this.loanRepaymentForm.get('month').setValue(dataMonth);
    this.loanRepaymentForm.get('day').setValue(dataDay);  
  }, (err: any) => {
    console.error(err);
  })
}
lastUpdateByCif(){
  this.apiService.getLoanDetails(this.loanaccountId).subscribe((resp:any)=>{
   
   this.fRepayment = this.datepipe.transform(resp.applicationDate,"yyyy-MM-dd");
 
  }, (err: any) => {
    console.error(err);
  })
}
  
}
