import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { PostOfferAmendmentSummaryComponent } from './post-offer-amendment-summary/post-offer-amendment-summary.component';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { PostOfferAmendmentStageSummaryComponent } from './post-offer-amendment-stage-summary/post-offer-amendment-stage-summary.component';

@Component({
  selector: 'app-post-offer-amend-stage',
  templateUrl: './post-offer-amend-stage.component.html',
  styleUrls: ['./post-offer-amend-stage.component.scss'],
  animations:rumangoAnimations
})
export class PostOfferAmendStageComponent implements OnInit {
  ispostOfferAmendStageDone: boolean;
  isOfferIssueRejectScreen: boolean;

  constructor(private router:Router,public dialog: MatDialog, private ls: LocalStoreService) { }

  ngOnInit(): void {
    this.ispostOfferAmendStageDone = this.ls.getItem('post-offer-amend-stage');
    this.isOfferIssueRejectScreen = this.ls.getItem('OfferIssueRejectScreen');
  }

  goTopostOfferAmendmentSummary(): void {
    const dialogRef = this.dialog.open(PostOfferAmendmentSummaryComponent, {
      width: '75%',
      backdropClass: 'dialog-bg-trans',
      height: '60%',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });

  }

  gotopostOfferAmendStageSummary(): void {
    this.router.navigate([`/loan/account/post-offer-amend-stage-summary/`]);

  }

  // goToOfferIssue(){
  //   // this.router.navigate([`/loan/account/loan-details`]);
  //   this.router.navigate([`/loan/account/offer-issue`]);
  // }

}
