import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-assessment-details',
  templateUrl: './assessment-details.component.html',
  styleUrls: ['./assessment-details.component.scss']
})
export class AssessmentDetailsComponent implements OnInit {

  public assessmentForm: FormGroup;

  userRecommendation=['Recomended For Approval', 'Recommended For Reject'];
  systemRecommendation=['Recomended For Approval', 'Recommended For Reject'];
  
  
  constructor( private router:Router, private snack: MatSnackBar, private cdr: ChangeDetectorRef, private apiService: ApiService, private _location: Location, private fb: FormBuilder,) { }

  ngOnInit(): void {
    this.buildAssessmentForm({});
  }

  buildAssessmentForm(item) {
    this.assessmentForm = this.fb.group({
      loanAccountId:[item.loanAccountId = "1234567"],
      requestedLoanAmount: [item.requestedLoanAmount],
      loanTenure: [item.loanTenure],
      rateOfInterest: [item.rateOfInterest],
      systemRecommendation: [item.systemRecommendation],
      userRecommendation: [item.userRecommendation || ''],
      loanAmountRecommendation: [item.loanAmountRecommendation || ''],
      approvedLoanAmount: [item.approvedLoanAmount],
      finalLoanTenure: [item.finalLoanTenure],
      finalRate: [item.finalRate],
    });  
  }


  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  next(){
    this.apiService.assessmentDetails(this.assessmentForm.value).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.cdr.markForCheck();
      }
    })
    this.router.navigate([`/loan/account-approval/offer-acceptReject-details`]);
  }
  
  goBack(){
    this.router.navigate([`/loan/account-approval/list`]);
  } 
}
