import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-underwriting-stage-summary',
  templateUrl: './underwriting-stage-summary.component.html',
  styleUrls: ['./underwriting-stage-summary.component.scss']
})
export class UnderwritingStageSummaryComponent implements OnInit {
  titleName = "UnderWriting Stage";
  checkBook: any;
  creditRatingValues: any;
  ValuationDetailsValues: any;
  opinionDetailsValue: any;
  loanAccountId: any;
  isApprovedSreen: any;
  loanAccountID: any;
  loanInfo: any;
  constructor(private router:Router,private api:ApiService,private ls: LocalStoreService) { }
  
  
    customerDetails: [{
      agency: 'ab',
      ratings: '60',
      remarks: 'This is a credit rating details'
    },{
      agency: 'cd',
      ratings: '80',
      remarks: 'This is a credit rating details'
    }]

  ngOnInit(): void {
    this.loanAccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountID = this.loanInfo.loanAccountId;
    }
    this.api.getApprovedStatus().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
 
    })
    this.loanAccountId = this.ls.getItem('loanAccountId');
    setTimeout(() => {
      this.getunderwritingStageSummaryDetails(this.loanAccountId);
      this.gettingCreditRatingDetail();
    }, 10);
   
    
  }
  getunderwritingStageSummaryDetails(loanAccountId: any) {
    this.api.gettingUnderWritingSummary(this.loanAccountId)
      .subscribe((summaryInfo: any) => {
       console.log(summaryInfo);
      //  this.creditRatingValues=summaryInfo.creditRatingInfo;
      //  console.log(this.creditRatingValues);
       this.ValuationDetailsValues=summaryInfo.valuationOfAssetInfo;
       console.log(this.ValuationDetailsValues)
       this.opinionDetailsValue=summaryInfo.legalOpinionInfo;
       console.log(this.opinionDetailsValue);
      //  this.buildCollateralForm(resp,true);
      });
  }
  gettingCreditRatingDetail() {
    this.api.gettingCreditRatingDetails(this.loanAccountID)
      .subscribe((creditDetails: any) => {
       console.log(creditDetails);
       this.creditRatingValues=creditDetails;
      //  this.buildCollateralForm(resp,true);
      });
  }
 
  
  goBack() {
    this.router.navigate([`/loan/account/list`]);
  }


  done() {
    this.router.navigate([`/loan/account/list`]);
  }

  
}
