  insertData(id: any, answer: any, qtype: string, mapId: any,index:any, applcantId: any) {
    console.log("Answer: ",answer,index, applcantId);
    console.log('score vaue', this.scorevalue);
    if (qtype == 'checkbox') {
      answer = answer.toString();
    }
    let dataUpdated: boolean = false ;
    let aplcntExists: boolean = false ;
    for (let m = 0; m < this.scoreCardMappingArray.length; m++) {
      if (this.scoreCardMappingArray[m].applicantId == applcantId ) {
        for (let n = 0; n < this.scoreCardMappingArray[m].scoreCardDetails.length; n++) {
          console.log(n, this.scoreCardMappingArray[m].scoreCardDetails[n]);
          if (this.scoreCardMappingArray[m].scoreCardDetails[n].scoreCardQuestionId == id) {
            this.scoreCardMappingArray[m].scoreCardDetails[n].scorecardAnswer = answer;
            dataUpdated = true;
          }
        }
        aplcntExists = true ;
      }
    }
    if (!dataUpdated) {
      let dataOject: any = {};
      dataOject.scoreCardQuestionId = id;
      dataOject.scorecardAnswer = answer;
      if (mapId != null) {
        dataOject.scoreCardMappingId = mapId;

      }
      if (!aplcntExists) {
        let idx = this.scoreCardMappingArray.length ;
        this.scoreCardMappingArray.push({applicantId: applcantId, scoreCardDetails: dataOject});
      } else {
        for (let m = 0; m < this.scoreCardMappingArray.length; m++) {
          if (this.scoreCardMappingArray[m].applicantId == applcantId ) {
              this.scoreCardMappingArray[m].scoreCardDetails.push(dataOject);
              console.log(this.scoreCardMappingArray[m].scoreCardDetails);
          }
        }
      }
      // console.log(dataOject,"Dataobject");
    }

    if (this.scoreCardMappingArray.length == this.scoreboardQuestionsArray.length) {
      let disableDoneBtn: number = 0;
      for (let m = 0; m < this.scoreCardMappingArray.length; m++) {
        for (let i = 0; i < this.scoreCardMappingArray[m].scoreCardDetails.length; i++) {
          let answerIs: any = this.scoreCardMappingArray[m].scoreCardDetails[i].scorecardAnswer;
          if (answerIs == '' || answerIs == null || answerIs == undefined) {
            disableDoneBtn++;
          }
        }
      }
      if (disableDoneBtn > 0) {
        this.disableDoneBtn = true;
      }
      else if (this.anyErrorExist == false && disableDoneBtn == 0) {
        this.disableDoneBtn = false;
      }
    }
    console.log("Map Array: ", this.scoreCardMappingArray)
    
  }

  validateInputValue(inputValue: any, index: any, validationName: string) {
    // console.log(inputValue, index);
    let eleid: any = '#inputError' + index;
    if (inputValue == '' || inputValue == null || inputValue == undefined) {
      this.renderer.setStyle(this.elRef.nativeElement.querySelector(eleid), 'visibility', 'visible');
    }
    else {
      if (!isNaN(inputValue) && validationName == "Name") {
        this.renderer.setStyle(this.elRef.nativeElement.querySelector(eleid), 'visibility', 'visible');
        this.anyErrorExist = true;
      }
      else {
        this.renderer.setStyle(this.elRef.nativeElement.querySelector(eleid), 'visibility', 'hidden');
        this.anyErrorExist = false;
      }
    }
  }

  validateSelectValue(selectValue: any, index: any) {
    // console.log(selectValue, index);
    let eleid: any = '#selectError' + index;
    if (selectValue == '' || selectValue == null || selectValue == undefined) {
      this.renderer.setStyle(this.elRef.nativeElement.querySelector(eleid), 'visibility', 'visible');
    }
    else {
      this.renderer.setStyle(this.elRef.nativeElement.querySelector(eleid), 'visibility', 'hidden');
    }
  }

