import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from '../../account.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';

@Component({
  selector: 'app-offerissue',
  templateUrl: './offerissue.component.html',
  styleUrls: ['./offerissue.component.scss']
})
export class OfferissueComponent implements OnInit {
  generateArr:string[]=['With Schedule','without Schedule'];
  approvedLoan: number;
  accountDetails: any;
  OfferIssueForm: FormGroup;
  showEditOption: boolean = false;
  otherDocumentArray: any[] = [];
  files: any[] = [];
  selectedFiles: any;
  showSpinn: boolean;
  fileName: any = [];
  docUrl: any;
  showDelete : boolean = true;
  selectedDay: number = 1;
  day: number;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  year: number;
  years: any[] = [];

  @ViewChild('report') report: NgForm;
  loanaccountID: any;
  accountId: any;
  documentUploaded: boolean=false;
  loanInfo: any;
  cifNumber: any;
 

  constructor(private router: Router,private ls: 
    LocalStoreService,private apiService:ApiService, private snack: MatSnackBar,private loader: AppLoaderService,
    @Inject(DOCUMENT) private _document: Document,private appApiService: ApplicationEntryStageService,
    private formBuilder: FormBuilder,private accountService : AccountService) { 
    this.OfferIssueForm = this.formBuilder.group({
      approvedLoanAmount : ['',[Validators.required]],
      applicantName:['',[Validators.required]],
      installmentFrequency:['',[Validators.required]],    
      rateOfInterest:['',[Validators.required]],  
      offerIssueDate:['',[Validators.required]],
      year:['',[Validators.required]],
      month:['',[Validators.required]],
      day:['',[Validators.required]],
      installmentType:['',[Validators.required]],
      generateOffer:['',[Validators.required]],
      interest:['',[Validators.required]],
      principal:['',[Validators.required]],
      installmentAmount:['',[Validators.required]],
      charges:['',[Validators.required]],
      loanTenure:[''],
    
    })
  }

  ngOnInit(): void {
    this.selectedYear = new Date().getFullYear();
  
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }

    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }

    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    let isOfferIssueScreen: boolean = JSON.parse(this.ls.getItem('OfferIssue'));
    //if (this.loanAccountId != null && isOfferIssueScreen) {
      setTimeout(() => {
        if (this.loanaccountID != null && isOfferIssueScreen) {
          this.showEditOption = true;
          
          this.getAccountDetails();
        } 
        else {
          console.log("else");
          this.showEditOption = false;
          this.buildOfferIssueForm();
        }
      }, 10);
   
    //this.getAccountType("account_type");
    this.fetchByCif();
    this.loanData();
    this.intrestData();
  }
  onFileSelected(event) {
    console.log("event -- ", event.target.files);
    // Iterate over selected files
    for (let file of event.target.files) {
      // Append to a list
      this.files.push({
        name: file.name,
        type: file.type
        // Other specs
      });
      this.snack.open('File Uploded', 'OK', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
    this.selectedFiles = event.target.files[0];
    console.log("selectedFiles: any", this.selectedFiles);
    if (event.target.files) {
      this.uploadDoc(event.target.files[0], this.selectedFiles, event);
    }

  }
  uploadDoc( event, url,  img) {
    console.log("event--->> ", url);
    const uploadData = new FormData();
    uploadData.append("file", url);
    console.log(uploadData);
  }

  getAccountDetails() {
    this.loader.open();
    this.apiService.getOfferIssueDetails(this.loanaccountID).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildOfferIssueForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });


  }
  get f() { return this.OfferIssueForm.controls; }
  get c() { return this.f.collaterals as FormArray; }

  deleteDoc(){
    this.documentUploaded=false;
    this.docUrl = ' ';
    this.fileName =  ' ';  
   // this.c.get('documentUrl').setValue('');
    //this.c.get('fileName').setValue('');
    this.showDelete = false;
    
  }
  uploadCollateralType(event) {
    console.log("event",event);
    //this.console.log("i=",i);
    this.showSpinn = true;
    const file:File = event.target.files[0];
    this.fileName = file.name;
    console.log("File name=",this.fileName );
    const data = {
      loanAccountId: this.loanaccountID,
      screenType: "offerIssue",
    };
    const uploadData = new FormData();
    uploadData.append("data", JSON.stringify(data));
    uploadData.append("file", file);
    console.log("file",file);
    console.log("data",data);
    this.apiService.uploadOfferDocument(uploadData).subscribe((res:any) => {
    console.log("Document Resp",res, res.fileUrl);
    this.docUrl=res.fileUrl;
    if(res && res.fileUrl){
     this.showSpinn = false;
     this.snack.open('Document Saved Successfully!', 'OK', { duration: 4000, verticalPosition: 'top', horizontalPosition: 'right' });
     this.documentUploaded=true;
   
    }
     });

  
  }
  

  buildOfferIssueForm(data?) {
    data.offerIssueDate = data ? moment(data.offerIssueDate).format() : '';
    this.OfferIssueForm = this.formBuilder.group({
      approvedLoanAmount : [data ? data.approvedLoanAmount:''],
      loanTenure:[data?data.loanTenure:''],
      applicantName:[data ? data.applicantName:''],
      installmentFrequency:[data ? data.installmentFrequency:''],    
      rateOfInterest:[data ? data.rateOfInterest:''],  
      offerIssueDate:[data ? data.offerIssueDate:''],
      generateOffer:[data ? data.generateOffer:''],
      installmentAmount:[data ? data.installmentAmount:''],
      principal:[data ? data.principal:''],
      interest:[data ? data.interest:''],
      charges:[data ? data.charges:''],
      year:[data ? data.loanTenure.split("-")[0]:''],
      month:[data ? data.loanTenure.split("-")[1]:''],
      day:[data ? data.loanTenure.split("-")[2]:''],
      installmentType: [data ? data.installmentType: '']
    
    })

  }
goBack(){
  this.router.navigate(["loan/account/offer-issue/list"]);
}
Submit(){
  let details:  any;
    //details.push(this.legalOpinion.value);
    let now = new Date();
    now=this.OfferIssueForm.value.opinionDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
    var aprloan=this.OfferIssueForm.value.approvedLoanAmount;
    aprloan= parseInt(aprloan);
    var rate=this.OfferIssueForm.value.rateOfInterest;
    rate= parseInt(rate);
    const renewData =
    this.OfferIssueForm.value.year +
    "-" +
    this.OfferIssueForm.value.month +
    "-" +
    this.OfferIssueForm.value.day;
  this.OfferIssueForm.get("loanTenure").setValue(renewData);
  delete this.OfferIssueForm.value.year;
  delete this.OfferIssueForm.value.month;
  delete this.OfferIssueForm.value.day;
    this.OfferIssueForm.value.principal =parseInt(this.OfferIssueForm.value.principal);
    this.OfferIssueForm.value.interest =parseInt(this.OfferIssueForm.value.interest);
    this.OfferIssueForm.value.installmentAmount =parseInt(this.OfferIssueForm.value.installmentAmount);
    this.OfferIssueForm.value.charges =parseInt(this.OfferIssueForm.value.charges);
    let payload={
      loanAccountId:this.loanaccountID,
      approvedLoanAmount:aprloan,
      loanTenure:renewData,
      applicantName:this.OfferIssueForm.value.applicantName,
      installmentFrequency:this.OfferIssueForm.value.installmentFrequency,
      rateOfInterest:rate,
      offerIssueDate:dateStringWithTime,
      generateOffer:this.OfferIssueForm.value.generateOffer,
      principal:this.OfferIssueForm.value.principal,
      interest:this.OfferIssueForm.value.interest,
      installmentAmount:this.OfferIssueForm.value.installmentAmount,
      charges:this.OfferIssueForm.value.charges,
      installmentType:this.OfferIssueForm.value.installmentType
    }
    console.log("payload",payload)
    this.apiService.upsertOfferIssue(payload).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Offer Issue Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
     
      }
    })
    
  this.Next();
}
Next(){
  this.ls.setItem('OfferIssue', true);
  this.ls.setItem('OfferIssueStageDone', true);
  this.accountService.refresh.next();
  this.router.navigate(["/loan/account/offerstage/list"]);

}
_keyPress1(event: any) {
  const pattern = /^[0-9]*\.?[0-9]*$/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
  const input = event.target.value;
  if (input.length === 0 && event.which === 48) {
    event.preventDefault();
  }
}

  


_keyPress(event: any) {
  const pattern = /[0-9]/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
}
onInput(event: any, flag) {
  if (flag) {
    this.approvedLoan = event.target.value;
  } else {
    this.approvedLoan = event;
  }

  console.log("this.salary)::", this.approvedLoan);
  let sal = this.approvedLoan.toString().replace(/,/g, '').split(".");
  sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  let final = sal.join(".");
  console.log("final", final);
  this.OfferIssueForm.controls.approvedLoanAmount.setValue(final);
}

letterOnly(event) {
  var charCode = event.keyCode;
          if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
              return true;
          else
              return false;
}

fetchByCif(){
  this.cifNumber=this.ls.getItem('cId');
  this.appApiService.fetchByCif(this.cifNumber).subscribe((res: any) => {
  this.OfferIssueForm.get('applicantName').setValue(res.customerInfoList[0].firstName +" "+ res.customerInfoList[0].lastName);
  }, (err: any) => {
    console.error(err);
  })
}

loanData(){
  console.log(this.loanaccountID);
  this.apiService.fetchByLoanAccountId(this.loanaccountID).subscribe((res:any)=>{    
    this.OfferIssueForm.get('approvedLoanAmount').setValue(res.approvedLoanAmount);        
    let dataYear = res.loanTenure.split("-")[0];
    let dataMonth = res.loanTenure.split("-")[1];
    let dataDay = res.loanTenure.split("-")[2];    
    this.OfferIssueForm.get('year').setValue(dataYear);
    this.OfferIssueForm.get('month').setValue(dataMonth);
    this.OfferIssueForm.get('day').setValue(dataDay);  
  }, (err: any) => {
    console.error(err);
  })
}
intrestData(){
  console.log(this.loanaccountID);
  this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((res:any)=>{    
    this.OfferIssueForm.get('rateOfInterest').setValue(res.rateOfInterest);
  })
}
}