import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-charge-details-summary',
  templateUrl: './loan-charge-details-summary.component.html',
  styleUrls: ['./loan-charge-details-summary.component.scss']
})
export class LoanChargeDetailsSummaryComponent implements OnInit {

  form1: Boolean;
  form2: Boolean;

  chargeFormIT: FormGroup;
  savingForm: FormGroup;
  accountId: any;
 // accountType: any;
  resp: { intrestType: string; amout: string; waiver: string; }[];
  loanAccountId: any;
  accountType: String = "Home Loan";
  loanInfo: any;

  constructor(private snack: MatSnackBar, private dialogRef: MatDialogRef<LoanChargeDetailsSummaryComponent>, private ls: LocalStoreService, private api: ApiService, private router: Router, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    this.chargeFormIT = this.formBuilder.group({
      chargeIT: new FormArray([]),
    });
  //   this.resp=[{"intrestType": "IT Charge",
  //   "amount":"$120","waiver":"Yes"},
  //   {"intrestType": "Handling Charge",
  //   "amount":"$100","waiver":'Yes'},
   
  // ]
  
  //   this.onChangeInterest(this.resp.length);
  //   this.in.patchValue(this.resp);



    this.api.getFlexcubeDetails("chargeDetails", this.accountType)
    .subscribe((response) => {
      this.onChangeInterest(response.length);
      this.in.patchValue(response);
    });
setTimeout(() => {
  this.api.loanCahrgeFetchById(this.loanAccountId).subscribe((resp) => {
    console.log("resp from backend ", this.resp);
    this.resp = resp;
    this.onChangeInterest(this.resp.length);
    this.in.patchValue(this.resp);
  });
}, 10);
  

  }
 

  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [''],
            amout: [''],
            waiver: [false],
            accountId: [''],
            createdDate: [''],
            craetedBy: [''],
            lastModifiedDate: [''],
            lastModifiedBy: ['']
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
 

  // convenience getters for easy access to form fields
  get f() {
    return this.chargeFormIT.controls;
  }
  get in() {
    return this.f.chargeIT as FormArray;
  }
  

  goBack() {
    this.router.navigate(["/home/application/enrichment/list"]);
  }

  onSubmit(form) {
    

    

  }
  edit() {
    this.dialogRef.close();
    this.router.navigate([`/loan/account/application-enrichment/charge-details`]);
  }

  check(accountType) {
    if (accountType) {
      console.log(this.snack.open)
      this.snack.open('charge detail is empty', 'Error', {
        duration: 4000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    }
  }
 
  _keyPress(event: any) {
    const pattern = /[a-zA-Z]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }
  
}


