import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';

@Component({
  selector: 'app-asset-valuation',
  templateUrl: './asset-valuation.component.html',
  styleUrls: ['./asset-valuation.component.scss']
})
export class AssetValuationComponent implements OnInit {
  bankValuationArr:string[]=['internal','external'];
  //bankValuationArr:string[]=['Asset Value'];
  areaArr:string[]=['sq','hectare'];
  assetValuation: FormGroup;
  marketValue: any;
  showEditOption: boolean = false;
  accountDetails: any;

  constructor(private formBuilder: FormBuilder,private ls: 
    LocalStoreService,private loader: AppLoaderService,private apiService:ApiService, private snack: MatSnackBar,
    private router: Router) { 
    this.assetValuation = this.formBuilder.group({
      bankValuation: ['',[Validators.required]],
      assetType: ['',[Validators.required]],
      propertyAreaSize: ['',[Validators.required]],
      mktValOfAsset: ['',[Validators.required]],
      assetValue: ['',[Validators.required]],
      valuationDate: ['',[Validators.required]],
      actualPropertyAreaSize: ['',[Validators.required]],
      actualPropertyAreaInUnits: ['',[Validators.required]],
    })
  }
  ngOnInit(): void {
    let isAssetValuationScreen: boolean = JSON.parse(this.ls.getItem('valuationAsset'));
    //if (this.loanAccountId != null && isAssetValuationScreen) {
    if (9999 != null && isAssetValuationScreen) {
      this.showEditOption = true;
      
      this.getAccountDetails();
    } 
    else {
      console.log("else");
      this.showEditOption = false;
      this.buildOfferIssueForm();
    }
  }
  getAccountDetails(){
    this.loader.open();
    //this.apiService.getAssetValuation(this.loacnAccountId).subscribe((accountDetails) => {
    this.apiService.getAssetValuation(112).subscribe((accountDetails) => {
      this.loader.close();
      this.accountDetails = accountDetails;
      console.log(this.accountDetails);
      this.buildOfferIssueForm(this.accountDetails);
    }, (err: any) => {
      this.loader.close();
    });

  }
  buildOfferIssueForm(data?) {
    this.assetValuation = this.formBuilder.group({
      bankValuation : [data ? data.bankValuation:''],
      assetType:[data ? data.assetType:''],
      propertyAreaSize:[data ? data.propertyAreaSize:''],
      mktValOfAsset:[data ? data.mktValOfAsset:''],
      assetValue:[data ? data.assetValue:''],    
      valuationDate:[data ? data.valuationDate:''],  
      actualPropertyAreaSize:[data ? data.actualPropertyAreaSize:''],
      actualPropertyAreaInUnits:[data ? data.actualPropertyAreaInUnits:''],

  })
}

  goBack(){
    this.router.navigate(["/loan/offer-issue/credit-rating"]);
  }
  submit(){
    let details:  any;
    //details.push(this.legalOpinion.value);
    let now = new Date();
    now=this.assetValuation.value.valuationDate;
    var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
    console.log("dateStringWithTime",dateStringWithTime);
    var pk=this.assetValuation.value.actualPropertyAreaSize;
    pk= parseInt(pk);
    var prSize=this.assetValuation.value.propertyAreaSize;
    prSize= parseInt(prSize);
    let payload={
      loanAccountId:parseInt("112"),
      bankValuation:this.assetValuation.value.bankValuation,
      assetType:this.assetValuation.value.assetType,
      propertyAreaSize:prSize,
      mktValOfAsset:this.assetValuation.value.mktValOfAsset,
      assetValue:this.assetValuation.value.assetValue,
      actualPropertyAreaInUnits:this.assetValuation.value.actualPropertyAreaInUnits,
      valuationDate:dateStringWithTime,
      actualPropertyAreaSize:pk,
    }
    console.log("payload",payload)
    this.apiService.saveOrValuationOfAsset(payload).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
     
      }
    })
    
  this.next();
}

  next(){
    this.ls.setItem('valuationAsset', true);
    this.router.navigate(["/loan/account/list/offer-issue/legal-opinion"]);
  }
  keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }
  //comma seprator
  onInput(event: any, flag) {
    if (flag) {
      this.marketValue = event.target.value;
    } else {
      this.marketValue = event;
    }
  
    console.log("this.salary)::", this.marketValue);
    let sal = this.marketValue.toString().replace(/,/g, '').split(".");
    sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = sal.join(".");
    console.log("final", final);
    this.assetValuation.controls.mktValOfAsset.setValue(final);
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

}
