import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-disbursement-summary',
  templateUrl: './loan-disbursement-summary.component.html',
  styleUrls: ['./loan-disbursement-summary.component.scss']
})
export class LoanDisbursementSummaryComponent implements OnInit {
  resp: any;
  CheckboxVar:boolean;
  loanaccountId: any;
  loanInfo: any;
  constructor(
    private ls: LocalStoreService,public api:ApiService,public dialogRef: MatDialogRef<LoanDisbursementSummaryComponent>, private router: Router) { }
  ngOnInit(): void {
    this.loanaccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.api.loanDisbursementFetchById(this.loanaccountId).subscribe(resp=>{
        this.resp=resp;
        this.CheckboxVar=resp.mutipleDisbursementRequired;
      }
  
        )
    }, 10);
  
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
     this.router.navigate([`/loan/account/application-enrichment/loan-disbursement`]);
  }
}