import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as moment from 'moment';

@Component({
  selector: 'app-offer-accept-reject',
  templateUrl: './offer-accept-reject.component.html',
  styleUrls: ['./offer-accept-reject.component.scss']
})
export class OfferAcceptRejectComponent implements OnInit {
  maxDate = new Date();
  minDate = new Date();
  offerForm: FormGroup;
  submitted: boolean;
  isOfferAcceptReject: boolean;
  loanaccountID: any;
  loanInfo: any;

  constructor(private api: ApiService,private formBuilder: FormBuilder, private router: Router,
     private ls: LocalStoreService,private dialogService : DialogService,private snack: MatSnackBar) { }

  ngOnInit(): void {
    this.isOfferAcceptReject = this .ls.getItem('OFFER_ACCEPT_REJECT_DONE');
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      if(this.isOfferAcceptReject==false){
        this.buildofferForm();
        }
        else{
         this.getOfferAcceptReject();
      }
    }, 10);
  
}

     getOfferAcceptReject() {
     this.api.getOfferAcceptReject('200')
     .subscribe((resp: any) => {
       console.log(resp);
       this.buildofferForm(resp,true);
   });
}
  buildofferForm(data?,Response?:boolean) {
    console.log(data);
    this.offerForm = this.formBuilder.group({
      accept:[''],
      applicantName: [''],
      loanTenure:[0],
      installmentFrequency:[''],
      installmentType:[''],
      principal:[0],
      installmentAmount:[0],
      offerIssueDate:[''],
      approvedLoanAmount:[0],
      rateOfInterest:[0],
      interest:[0],
      charges:[0],
      generateOffer:[''],
      offerExpiryDate:[''],   
      dateOfOfferAcceptOrReject:[''],
      loanAccountId:[10],
      offerAcceptOrRejectId:['']
    });
 }

_keyPress1(event: any) {
  const pattern = /^[0-9]*\.?[0-9]*$/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();
  }
  const input = event.target.value;
  if (input.length === 0 && event.which === 48) {
    event.preventDefault();
  }
}
letterOnly(event) {
  var charCode = event.keyCode;
  if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
    return true;
  else
    return false;
} 
goBack() {
  this.router.navigate(['loan/account/account-creation-stage']);
}


onSubmit() {
  this.offerForm.value.offerIssueDate= moment(this.offerForm.value.offerIssueDate).format('YYYY-MMM-DD HH:mm:ss');
  this.offerForm.value.generateOffer= moment(this.offerForm.value.generateOffer).format('YYYY-MMM-DD HH:mm:ss"');
  this.offerForm.value.offerExpiryDate= moment(this.offerForm.value.offerExpiryDate).format('YYYY-MMM-DD HH:mm:ss');
  this.offerForm.value.dateOfOfferAcceptOrReject= moment(this.offerForm.value.dateOfOfferAcceptOrReject).format('YYYY-MMM-DD HH:mm:ss');
    this.api.saveOfferAcceptReject(this.offerForm.value).subscribe((data: any) => {
      console.log(data);
     
      if (data === 200 || data !== null) {
        this.snack.open(' Credit Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.ls.setItem('OFFER_ACCEPT_REJECT_DONE', true);
        this.router.navigate(['loan/account/account-creation-stage/account-create-details']);
      }
      // else if (data === 500) {
      //   this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
      //     duration: 4000,
      //     verticalPosition: 'top',
      //     horizontalPosition: 'right'
      //   });
      // }
    }, err => {
      console.log(err);
    });
  }
 

}
