import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';
import { Router } from '@angular/router';
import { AssessmentSummaryComponent } from './assessment-summary/assessment-summary.component';
import { LoanViewSummaryComponent } from './loan-view-summary/loan-view-summary.component';
import { OfferAcceptRejectSumaryComponent } from './offer-accept-reject-summary/offer-accept-reject-summary.component';

@Component({
  selector: 'app-account-approval-stage',
  templateUrl: './account-approval-stage.component.html',
  styleUrls: ['./account-approval-stage.component.scss'],
  animations:rumangoAnimations
})
export class AccountApprovalStageComponent implements OnInit {

  isAccountApproveStageDone: boolean = false;
  isAssessmentStageDone: boolean = false;
  isOfferAcceptRejectStageDone: boolean = false;
  isLoanSummaryStageDone: boolean = false;

  constructor( private router: Router,public dialog: MatDialog,private ls: LocalStoreService,private cdr:ChangeDetectorRef ,private accountService:AccountService) { }

  ngOnInit(): void {
    this.isAccountApproveStageDone = this.ls.getItem('account-approval');
    this.isAssessmentStageDone = this.ls.getItem('assessment-details');
    this.isOfferAcceptRejectStageDone = this.ls.getItem('offer-acceptReject-details');
    this.isLoanSummaryStageDone = this.ls.getItem('loan-summary-details');
    this.accountService.refresh.subscribe((res: any) => {
      
      // Parent 1
      this.isAccountApproveStageDone = this.ls.getItem('account-approval');
      this.isAssessmentStageDone = this.ls.getItem('assessment-details');
      this.isOfferAcceptRejectStageDone = this.ls.getItem('offer-acceptReject-details');
      this.isLoanSummaryStageDone = this.ls.getItem('loan-summary-details');
      // Parent 1 childs nav
      
    
      this.cdr.markForCheck();
    });
  }

  // openDialog(): void {
  //   let dialogRef = this.dialog.open(AssessmentSummaryComponent, {
  //     width: '800px',
  //     height:'600px',
  //     // data: { name: this.name, animal: this.animal }
  //   });
  // }

  goToAssessmentSummary(): void {
    const dialogRef = this.dialog.open(AssessmentSummaryComponent, {
      width: '75%',
      backdropClass: 'dialog-bg-trans',
      height: '50%',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  openDialog1(): void {
    const dialogRef = this.dialog.open(OfferAcceptRejectSumaryComponent, {
      width: '75%',
      backdropClass: 'dialog-bg-trans',
      height: '60%',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });

  }

  openDialog2(): void {
    const dialogRef = this.dialog.open(LoanViewSummaryComponent, {
      width: '75%',
      backdropClass: 'dialog-bg-trans',
      height: '70%',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });

  }

  gotoAccountApprovalSummary(){
    this.router.navigate([`/loan/account-approval/account-approval-summary-details/`]);
  }
}
