import { Component, Input, OnInit } from '@angular/core';
import { FormArray,FormBuilder, FormGroup } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-enrichment-view-summary',
  templateUrl: './loan-enrichment-view-summary.component.html',
  styleUrls: ['./loan-enrichment-view-summary.component.scss']
})
export class LoanEnrichmentViewSummaryComponent implements OnInit {
  
  appEntrySummaryView: boolean = false;
  accountDetails: any;
  interestForm: FormGroup;
  chargeFormIT: FormGroup;
  savingForm:FormGroup;
  loanForm:FormGroup;
  interestForm1:FormGroup;
  accountId: any;
  //for loan interest details
  loanInterestResp: {
    intrestType: string;
    // interestRateApplicable: string;
    intrestRateApplicable: string;
    // margin: number;
    marginIn: number;
    effectiveRate: number;
  }[];
  intererstInfo: {
    intrestType: string;
    //interestRateApplicable: string;
    intrestRateApplicable: string;
    //margin: number;
    marginIn: number;
    effectiveRate: number;
  }[];
  CheckboxVar:boolean;
  accountType: String = "Home Loan";//for charge details summary
  loanChargeResp: { interestType: string; amount1: string; waiver: string; }[];
  loanRepaymentResp:any;

  //Account Services Summary
  maturity: any;
  revision: any;
  holiday: any;
  cascadeVarMaturity: boolean;
  cascadeVarRevison: boolean;
  cascadeVarHoliday: boolean;
  ignoreHolidayVarMaturity: boolean;
  ignoreHolidayVarRevison: boolean;
  ignoreHolidayVarHoliday: boolean;
  accServResp: any;
  loanDisbursementResp:any;
  accServiceInfo:any;
  loanAccountId:any;
  disbursementInfo: any;
  repaymentInfo: any;
  chargeInfo: any;
  isApprovedSreen: any;
  loanInfo: any;
  
  constructor(private formBuilder:FormBuilder,private ls: LocalStoreService,private snack: MatSnackBar,private api:ApiService,private router: Router) { }

  ngOnInit(): void {
  //  this.accountId = this.ls.getItem('accountId');
    //this.accountType=this.ls.getItem('accountType');
    this.api.getApprovedStatus().subscribe((resp)=>{
      console.log("message",resp);
      this.isApprovedSreen=resp;
 
    })
    this.loanAccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getLoanEnrichmentDetails(this.loanAccountId);
    }, 10);
   
    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    this.savingForm = this.formBuilder.group({
      saving: new FormArray([]),
    });
    this.chargeFormIT = this.formBuilder.group({
      chargeIT: new FormArray([]),
    });
    this.loanForm = this.formBuilder.group({
      loan: new FormArray([]),
    });

    // this.chargeFormIT = this.formBuilder.group({
    //   chargeInfo: new FormArray([]),
    // });
    // this.interestForm1 = this.formBuilder.group({
    //   chargeInfo: new FormArray([]),
    // });




//for Charge Details
  this.api.getFlexcubeDetails("chargeDetails", this.accountType)
  .subscribe((response) => {
    this.onloanCharge(response.length);
    this.in2.patchValue(response);
  });

// this.api.loanCahrgeFetchById(this.loanAccountId).subscribe((loanChargeResp) => {
//   console.log("loan charge details resp from backend ", this.loanChargeResp);
//   this.loanChargeResp = loanChargeResp;
//   this.onloanCharge(this.loanChargeResp.length);
//   this.in2.patchValue(this.loanChargeResp);
// });


  }

  getLoanEnrichmentDetails(loanAccountId:any){
    this.api.getLoanEnrichmentSummmary(this.loanAccountId)
      .subscribe((summaryInfo: any) => {
       console.log(summaryInfo);
       this.intererstInfo=summaryInfo.intererstInfo;
       console.log(this.intererstInfo);
       this.onChangeInterest(this.intererstInfo.length);
       this.in.patchValue(this.intererstInfo);
       this.disbursementInfo=summaryInfo.disbursementInfo;
       this.CheckboxVar=this.disbursementInfo.mutipleDisbursementRequired;
       console.log(this.disbursementInfo)
       this.repaymentInfo=summaryInfo.repaymentInfo;
       console.log(this.repaymentInfo);
       this.chargeInfo = summaryInfo.chargeInfo;
       this.onloanCharge(this.chargeInfo.length);
       this.in2.patchValue(this.chargeInfo);
      this.accServiceInfo=summaryInfo.accServiceInfo;
      this.maturity= this.accServiceInfo.serviceDetails.find(obj => obj.serviceName === 'Maturity')
      this.revision= this.accServiceInfo.serviceDetails.find(obj => obj.serviceName === 'Revision')
      this.holiday=this.accServiceInfo.serviceDetails.find(obj => obj.serviceName === 'Holiday Preference')
      this.cascadeVarMaturity=  this.maturity.cascadeSchedules;
      this.cascadeVarRevison=   this.revision.cascadeSchedules;
      this.cascadeVarHoliday= this.holiday.cascadeSchedules; 
      this.ignoreHolidayVarMaturity=  this.maturity.ignoreHolidays;
      this.ignoreHolidayVarRevison=   this.revision.ignoreHolidays;
      this.ignoreHolidayVarHoliday= this.holiday.ignoreHolidays; 
      });
  }


  //for loan interest details
  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
           
            intrestType: [""],
            //interestRateApplicable: [""],
            intrestRateApplicable: [""],
            //margin: [""],
            marginIn: [""],
            effectiveRate: [""],
           // accountId: [this.accountId],
            loanAccountId:[this.loanAccountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  //for loan interest details
  onChangeSaving(e) {
    const numberOfItems = e || 0;
    if (this.is.length < numberOfItems) {
      for (let i = this.is.length; i < numberOfItems; i++) {
        this.is.push(
          this.formBuilder.group({
            intrestType: [""],
            // interestRateApplicable: [""],
            intrestRateApplicable: [""],
            // margin: [""],
            marginIn: [""],
            effectiveRate: [""],
           // accountId: [this.accountId],
            loanAccountId:[this.loanAccountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.is.length; i >= numberOfItems; i--) {
        this.is.removeAt(i);
      }
    }
  }
  //for loan interest details
  onChangeLoan(e) {
    const numberOfItems = e || 0;
    if (this.iloan.length < numberOfItems) {
      for (let i = this.iloan.length; i < numberOfItems; i++) {
        this.iloan.push(
          this.formBuilder.group({
           
            intrestType: [""],
            //interestRateApplicable: [""],
            intrestRateApplicable: [""],
            // margin: [""],
            marginIn: [""],
            effectiveRate: [""],
            //accountId: [this.accountId],
            loanAccountId:[this.loanAccountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.iloan.length; i >= numberOfItems; i--) {
        this.iloan.removeAt(i);
      }
    }
  }


  //loan charge details
  onloanCharge(e) {
    const numberOfItems = e || 0;
    if (this.in2.length < numberOfItems) {
      for (let i = this.in2.length; i < numberOfItems; i++) {
        this.in2.push(
          this.formBuilder.group({
            intrestType: [""],
            amout: [""],
            waiver: [true],
            loanAccountId: [this.loanAccountId],
            chargeDetailsId: [""],
          })
        );
      }
    } else {
      for (let i = this.in2.length; i >= numberOfItems; i--) {
        this.in2.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f2() {
    return this.chargeFormIT.controls;
  }
  get in2() {
    return this.f2.chargeIT as FormArray;
  }
 



  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }
  get fs() {
    return this.savingForm.controls;
  }
  get is() {
    return this.fs.saving as FormArray;
  }
  get floan() {
    return this.loanForm.controls;
  }
  get iloan() {
    return this.floan.loan as FormArray;
  }
  get fCharge() {
    return this.chargeFormIT.controls;
  }
  // get inCharge() {
  //   return this.fCharge.chargeIT as FormArray;
  // }


  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("chargeInfo detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }
  checkbox(event) {
    console.log(event.checked);
  }


  assignAccountType(accountType) {
    this.accountType = accountType;
  }

  Done(){
    this.router.navigate([`/loan/account/list/underwritingStage`]);
  }
  Back(){
    this.router.navigate([`/loan/account/application-enrichment`]);
  }

}
