import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ApiService } from 'app/shared/services/api.service';

@Component({
  selector: 'app-offer-accept-reject-details',
  templateUrl: './offer-accept-reject-details.component.html',
  styleUrls: ['./offer-accept-reject-details.component.scss']
})
export class OfferAcceptRejectDetailsComponent implements OnInit {

  public offerAcceptRejectForm: FormGroup;
  maxDate = new Date();
  minDate = new Date();
  customerResponse=['Accept', 'Reject'];
  

  constructor( private router:Router, private snack: MatSnackBar, private cdr: ChangeDetectorRef, private apiService: ApiService, private _location: Location, private fb: FormBuilder,) { }

  ngOnInit(): void {
    this.buildOfferAcceptRejectForm({});
  }

  buildOfferAcceptRejectForm(data) {
    this.offerAcceptRejectForm = this.fb.group({
      offerAcceptOrRejectId:[data.offerAcceptOrRejectId = '12345'],
      loanAccountId : '7654',  
      applicantName: [data.applicantName],
      approvedLoanAmount: [data.approvedLoanAmount],
      loanTenure: [data.loanTenure],
      installmentType: [data.installmentType],
      installmentFrequency: [data.installmentFrequency],
      rateOfInterest: [data.rateOfInterest],
      principal: [data.principal],
      interest: [data.interest],
      installmentAmount: [data.installmentAmount],
      charges: [data.charges],
      dateOfOfferAcceptOrReject: [new Date(data.dateOfOfferAcceptOrReject)],
      generateOffer: [new Date(data.generateOffer)],
      offerIssueDate: [new Date(data.offerIssueDate)],
      offerExpiryDate: [new Date(data.offerExpiryDate)],
    });  
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  

  convertDate(value) {
    // let date = moment(value).format('YYYY-MMM-DD');
    // let date = moment(value).format('YYYY-MMM-DD hh:mm:ss');
    let date = moment(value).format("YYYY-MMM-DD HH:mm:ss");
    console.log("date", date);
    if (date)
      return date;
  }

  
  next(){

    let generateDate = this.convertDate(this.offerAcceptRejectForm.controls.generateOffer.value);
    this.offerAcceptRejectForm.value['generateOffer'] = generateDate;
    let offerAcceptDate = this.convertDate(this.offerAcceptRejectForm.controls.dateOfOfferAcceptOrReject.value);
    this.offerAcceptRejectForm.value['dateOfOfferAcceptOrReject'] = offerAcceptDate;
    let issueDate = this.convertDate(this.offerAcceptRejectForm.controls.offerIssueDate.value);
    this.offerAcceptRejectForm.value['offerIssueDate'] = issueDate;
    let expiryDate = this.convertDate(this.offerAcceptRejectForm.controls.offerExpiryDate.value);
    this.offerAcceptRejectForm.value['offerExpiryDate'] = expiryDate;

    this.apiService.offerDetails(this.offerAcceptRejectForm.value).subscribe(data => {
      console.log("data",data);
      if(data != null){
        this.snack.open('Data Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.cdr.markForCheck();
      }
    })

    

    this.router.navigate([`/loan/account-approval/loan-summary-details`]);
  }
  
  goBack(){
    this.router.navigate([`/loan/account-approval/assessment-details`]);
  } 

}
