import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-loan-summary-details',
  templateUrl: './loan-summary-details.component.html',
  styleUrls: ['./loan-summary-details.component.scss']
})
export class LoanSummaryDetailsComponent implements OnInit {

  userRecommendation=['Recomended For Approval', 'Recommended For Reject'];
  public loanSummaryForm: FormGroup;

  constructor( private router:Router, private _location: Location, private fb: FormBuilder,) { }

  ngOnInit(): void {
   this.buildAssessmentForm()
  }
 
  buildAssessmentForm() {
    this.loanSummaryForm = this.fb.group({
      applicantName: [''],
      accountType: [''],
      accountBranch: [''],
      productCode: [''],
      productName: [''],
      hostProductCode: [''],
      hostProductDescription: [''],
      loanAmount: [''],
      loanTenure: [''],
      interestRate: [''],
      repaymentMethod: [''],
      repaymentFrequency: [''],
      applicationDate: [''],
      assessmentApprovalMethod: [''],
      offerApprovedDate: [''],
      offerAcceptedDate: [''],
      approverID: [''],
      stage: [''],
      date: [''],
      offerIssueDate: [''],
      userRecommendation: [''],
      userAction: [''],

    });  
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();

    }
  }

  next(){
    this.router.navigate([`/loan/account-approval/offer-acceptReject-details`]);
  }
  
  goBack(){
    this.router.navigate([`/loan/account-approval/list`]);
  } 
}
