import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-account-creation-summary',
  templateUrl: './account-creation-summary.component.html',
  styleUrls: ['./account-creation-summary.component.scss']
})
export class AccountCreationSummaryComponent implements OnInit {
  titleName = "Account Creation Stage";
  accountCreateValue: any;
  offerAcceptRejectvalue: any;
  loanaccountID: any;
  loanInfo: any;

  constructor(private router:Router,private api: ApiService,private ls:LocalStoreService) { }

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.getOfferAcceptReject();
    this.getAccountCreateDetails();
  }
  goBack() {
    this.router.navigate([`/loan/account/list`]);
  }


  done() {
    this.router.navigate([`/loan/account/list`]);
  }
  getOfferAcceptReject() {
    this.api.getOfferAcceptReject('200')
     .subscribe((offerAcceptReject: any)=> {
      console.log(offerAcceptReject);
      this.offerAcceptRejectvalue=offerAcceptReject;
      
    });
  }
  getAccountCreateDetails() {
    this.api.getAccountCreateDetails(this.loanaccountID)
     .subscribe((accountCreateDetails: any)=> {
      console.log(accountCreateDetails);
      this.accountCreateValue=accountCreateDetails;
      
    });
  }

}
