  onSubmit() {
    this.submitted = true;
    console.log('saveapplicant')
    this.isCifValid= false;
    // if (this.customerInfoForm.invalid) {
    //   console.log("test");

    //   return;
    // }
    // this.loader.open();

    let payloadObj: any = {};
    const control = this.customerInfoForm.controls['applicantDetails'].value;
    console.log(control);
    let addressObj: any = {};
    let custIdProofs: any = {};
    let custIdProofsArray: any[] = [];
    let addressObjArray: any[] = [];

    payloadObj.customerId = this.ls.getItem("cId");
    payloadObj.cifNumber = control.cifNumber;
    payloadObj.dependentId = control.dependentId;
    payloadObj.existingCustomer = control.existingCustomer;
    payloadObj.primaryCustomer = control.primaryCustomer;
    payloadObj.residenceStatus = control.residenceStatus;
    payloadObj.placeOfBirth = control.placeOfBirth;
    payloadObj.countryOfResidence = control.countryOfResidence;
    payloadObj.occupationType = control.occupationType;
    payloadObj.phoneNumber = control.phoneNumber;
    payloadObj.primaryEmailAdress = control.primaryEmailAdress;
    payloadObj.prefix = control.prefix;
    payloadObj.firstName = control.firstName;
    payloadObj.middleName = control.middleName;
    payloadObj.lastName = control.lastName;
    payloadObj.cifNumber = control.cifNumber;
    payloadObj.gender = control.gender;
    payloadObj.dateOfBirth = (control.dateOfBirth);
    
    payloadObj.birthCountry = control.birthCountry;
    payloadObj.maritalStatus = control.maritalStatus;
    payloadObj.nationality = control.nationality;
    payloadObj.loanAccountId = this.loanaccountID;
    payloadObj.accountType="loan";
    payloadObj.landlineNumber = control.landlineNumber;
    payloadObj.ownership = this.customerInfoForm.get('ownership').value;
    payloadObj.numOfApplicants = this.customerInfoForm.get('numOfApplicants').value;
    addressObj.address1 = control.address1;
    addressObj.address2 = control.address2;
    addressObj.country = control.country;
    addressObj.state = control.state;
    addressObj.city = control.city;
    addressObj.zipCode = control.zipCode;
    addressObj.addressType = control.addressType;
    addressObjArray.push(addressObj);

    custIdProofs.expiryDate = (control.expiryDate);
    custIdProofs.idType = control.idType;
    custIdProofs.idNumber = control.idNumber;
    custIdProofsArray.push(custIdProofs);

    payloadObj.userAddress = addressObjArray;
    payloadObj.custIdProofs = custIdProofsArray;

    console.log("payloadObj",payloadObj);
    this.isChecked=false;
    this.disabled=false;
    this.appApiService.createAndUpdateAccountants(payloadObj)
      .subscribe((data: any) => {
        console.log("data::",data)
        this.closeDetails();
        this.loader.close();
        console.log("Response of service 2 ---> ", data)
        if (data.status === 200) {
        
          this.getListOfAccounts();
          this.snack.open(' Applicant is creating successfully!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });

          if(data.custDependentModel.primaryCustomer==true){
            this.ls.setItem('primaryCustomerName', data.custDependentModel.firstName + ' ' + data.custDependentModel.lastName);
          }
   
        this.customerInfoForm.get('ownership').patchValue(data.custDependentModel.ownership);
        this.customerInfoForm.get('numOfApplicants').patchValue(data.custDependentModel.numOfApplicants);

          if(this.customerInfoForm.get('ownership').value == 'Self'){
            this.goToNext();
          }
        }
      }, (err: any) => {
        console.log(err);
        this.loader.close();
        this.snack.open('Error', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
  }



