  goBack() {
    // console.log("previousUrl ::", this.previousUrl);
    // this.router.navigate([this.previousUrl]);
    if(this.productName == "Classic Home Loan") {
      this.router.navigate([`/loan/account/asset-details`]);
    }
    else if(this.productName == "Vehicle Loan") {
      this.router.navigate([`/loan/account/vehicle-details`]);
    }
    else {
      this.router.navigate([`/loan/account/admission-details`]);
    }

  }

  goToNext() {
   // this.onSubmit();
    this.ls.setItem('CUSTOMERINFO_DETAIL_DONE', true);
   
  //  if(this.ls.getItem.accountType!=)
  this.accountService.refresh.next();
  //  this.router.navigate([`/home/application/mandate/${this.accountId}`]);
  this.ls.setItem('LoanNoOfApplicants', this.customerInfoForm.get('numOfApplicants').value);
  this.ls.setItem('ownership_value', this.customerInfoForm.get('ownership').value);
  
  this.router.navigate([`/loan/account/mandate-details`]);
  }

  isAllSameDigit(event) {
    let count = 0, number = event.target.value;
    if (number != '') {
      for (var i = 0; i < number.length; i++) {
        if (0 == number[i]) {
          count++;
        }
      }
      console.log('number :: ', number);
      console.log('isAllSameDigitVal :: ', count , number.length)
      if (count == number.length) {
        console.log('if loop')
        this.isAllSameDigitVal = true;
      }
      else {
        this.isAllSameDigitVal = false;
      }
    }else{
      this.isAllSameDigitVal = false;
    }
  }

  ngOnDestroy(): void {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
    this.dataService.sendNavParam({});
  }

