import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { AppLoaderService } from "app/shared/services/app-loader/app-loader.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { AccountService } from "../../account.service";


@Component({
  selector: "app-loan-intrest-details",
  templateUrl: "./loan-intrest-details.component.html",
  styleUrls: ["./loan-intrest-details.component.scss"],
})
export class LoanIntrestDetailsComponent implements OnInit {
  interestForm: FormGroup;
  accountId: any;
  intrestFlag: Boolean;
  interestResp: any;
  showEditOption: boolean = false;
  resp: {
    intrestType: string;
    intrestRateApplicable: string;
    marginIn: number;
    effectiveRate: number;
  }[];
  accountType: String = "Home Loan";
  loanaccountID: any;
  loanInfo: any;
  constructor(
    private accountService: AccountService,
    private snack: MatSnackBar,
    private ls: LocalStoreService,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder,
    private loader: AppLoaderService,
  ) {}

  ngOnInit(): void {
    this.accountId = this.ls.getItem("accountId");
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    // this.resp=[{"intrestType": "Fixed Rate",
    // "interestRateApplicable":"5%","marginIn":60,"effectiveRate":10},
    // {"intrestType": "Penalty Rate",
    // "interestRateApplicable":"5%","marginIn":60,"effectiveRate":10},
    // {"intrestType": "Tax Rate",
    // "interestRateApplicable":"5%","marginIn":60,"effectiveRate":10}]

    let isLoanInterestDetails = this.ls.getItem('LOAN_INTERST_DETAIL_DONE');
    setTimeout(() => {
      if (this.loanaccountID != null && isLoanInterestDetails) {
        this.showEditOption = true;
        
        this.getInterestDetails();
      } 
      else {
        console.log("else");
        this.showEditOption = false;
        this.api
        .getFlexcubeDetails("intrestDetails", this.accountType)
        .subscribe((response) => {
          this.onChangeInterest(response.length);
          this.in.patchValue(response);
        });
      }
    }, 10);
  

    this.interestForm = this.formBuilder.group({
      interestInfo: new FormArray([]),
    });
  
  }
  assign(accountId) {
    // this.accountId = accountId;
  }

  getInterestDetails(){
    this.loader.open();
    this.api.loanInterestFetchById(this.loanaccountID).subscribe((resp) => {
      this.loader.close();
      console.log("resp from backend ", this.resp);
      this.resp = resp;
      this.onChangeInterest(this.resp.length);
      this.in.patchValue(this.resp);
    }, (err: any) => {
      this.loader.close();
    });

    
  }
  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            rateCode:["",Validators.required],
            intrestType: ["", Validators.required],
            intrestRateApplicable: ["", Validators.required],
            marginIn: [
              "",
              [Validators.required, Validators.max(100), Validators.min(0)],
            ],
            effectiveRate: [
              "",
              [Validators.required, Validators.max(100), Validators.min(0)],
            ],
            loanAccountId: [this.loanaccountID],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
            loanInterestId: [""],
            id: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }

  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interestInfo as FormArray;
  }

  goBack() {
 
    this.router.navigate([
      `/loan/account/application-enrichment`,
    ]);
  }
  
  appendPercentage(event) {
    // console.log(event.key)
    console.log(event.target.value);

    if (!event.target.value.length) return;

    if (event.target.value.length === 1) {
      event.target.value = `${event.target.value}%`;
    }
    if (event.target.value.length > 1) {
      event.target.value = `${event.target.value.slice(0, -1)}%`;
    }
  }

  onSubmit() {
    this.api.loanintrestDetailsSave(this.interestForm.value).subscribe(
      (resp) => {
        this.snack.open("Interest Details Saved!", "OK", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.next();
        console.log("backend response from intrest saving", resp);
      },
      (err) => {
        this.snack.open(
          "Not able to connect to server!",
          "INTERNAL SERVER ERROR",
          {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          }
        );
      }
    );
    
  }
  
  next() {
    this.ls.setItem('LOAN_INTERST_DETAIL_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/loan/account/application-enrichment/loan-disbursement`]);
  }
  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("Interest detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }
}