import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
@Component({
  selector: 'app-account-service-summary',
  templateUrl: './account-service-summary.component.html',
  styleUrls: ['./account-service-summary.component.scss']
})
export class AccountServiceSummaryComponent implements OnInit {
  maturity: any;
  revision: any;
  holiday: any;
  cascadeVarMaturity: boolean;
  cascadeVarRevison: boolean;
  cascadeVarHoliday: boolean;
  ignoreHolidayVarMaturity: boolean;
  ignoreHolidayVarRevison: boolean;
  ignoreHolidayVarHoliday: boolean;
  resp: any;
  loanAccountId: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<AccountServiceSummaryComponent>, private router: Router,private api:ApiService,private ls: LocalStoreService) { }
  
  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.api.AccountServicesFetchById(this.loanAccountId).subscribe(resp=>{
        console.log("resp from backend ",resp)
        if(resp){
          this.resp=resp;
        this.maturity= resp.serviceDetails.find(obj => obj.serviceName === 'Maturity')
        this.revision= resp.serviceDetails.find(obj => obj.serviceName === 'Revision')
        this.holiday=resp.serviceDetails.find(obj => obj.serviceName === 'Holiday Preference')
        this.cascadeVarMaturity=  this.maturity.cascadeSchedules;
        this.cascadeVarRevison=   this.revision.cascadeSchedules;
        this.cascadeVarHoliday= this.holiday.cascadeSchedules; 
        this.ignoreHolidayVarMaturity=  this.maturity.cascadeSchedules;
        this.ignoreHolidayVarRevison=   this.revision.cascadeSchedules;
        this.ignoreHolidayVarHoliday= this.holiday.cascadeSchedules; 
      }
  //       cascadeSchedules: true
  // holidayCheck: "Uncheck"
  // ignoreHolidays: true
  // moveAcrossMonth: "month 2"
            })
    }, 10);
   
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    // this.router.navigate([`/loan/account/list/underwritingStage/legalopinion`]);

     this.router.navigate([`/loan/account/application-enrichment/account-services`]);
  }
}