import { Component, OnInit } from "@angular/core";
import { FormArray, FormBuilder, FormGroup } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";

@Component({
  selector: "app-loan-interest-summary",
  templateUrl: "./loan-interest-summary.component.html",
  styleUrls: ["./loan-interest-summary.component.scss"],
})
export class LoanInterestSummaryComponent implements OnInit {
  interestForm: FormGroup;
  savingForm: FormGroup;
  loanForm: FormGroup;
  accountType: any;
  accountId: any;
  resp: {
    intrestType: string;
    intrestRateApplicable: number;
    marginIn: number;
    effectiveRate: number;
  }[];
  loanAccountId: any;
  loanInfo: any;
  constructor(
    private ls: LocalStoreService,
    private snack: MatSnackBar,
    private dialogRef: MatDialogRef<LoanInterestSummaryComponent>,
    private api: ApiService,
    private router: Router,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    this.interestForm = this.formBuilder.group({
      interest: new FormArray([]),
    });
    this.savingForm = this.formBuilder.group({
      saving: new FormArray([]),
    });

    this.loanForm = this.formBuilder.group({
      loan: new FormArray([]),
    });
setTimeout(() => {
  this.api.loanInterestFetchById(this.loanAccountId).subscribe((resp) => {
    console.log("resp from backend ", this.resp);
    this.resp = resp;
    this.onChangeInterest(this.resp.length);
    this.in.patchValue(this.resp);
  });
}, 10);
  
  }
  assignAccountType(accountType) {
    this.accountType = accountType;
  }

  onChangeInterest(e) {
    const numberOfItems = e || 0;
    if (this.in.length < numberOfItems) {
      for (let i = this.in.length; i < numberOfItems; i++) {
        this.in.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            accountId: [this.accountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.in.length; i >= numberOfItems; i--) {
        this.in.removeAt(i);
      }
    }
  }
  onChangeSaving(e) {
    const numberOfItems = e || 0;
    if (this.is.length < numberOfItems) {
      for (let i = this.is.length; i < numberOfItems; i++) {
        this.is.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            accountId: [this.accountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.is.length; i >= numberOfItems; i--) {
        this.is.removeAt(i);
      }
    }
  }
  onChangeLoan(e) {
    const numberOfItems = e || 0;
    if (this.iloan.length < numberOfItems) {
      for (let i = this.iloan.length; i < numberOfItems; i++) {
        this.iloan.push(
          this.formBuilder.group({
            intrestType: [""],
            intrestRateApplicable: [""],
            marginIn: [""],
            effectiveRate: [""],
            accountId: [this.accountId],
            createdDate: [""],
            createdBy: [""],
            lastModifiedDate: [""],
            lastModifiedBy: [""],
          })
        );
      }
    } else {
      for (let i = this.iloan.length; i >= numberOfItems; i--) {
        this.iloan.removeAt(i);
      }
    }
  }
  // convenience getters for easy access to form fields
  get f() {
    return this.interestForm.controls;
  }
  get in() {
    return this.f.interest as FormArray;
  }
  get fs() {
    return this.savingForm.controls;
  }
  get is() {
    return this.fs.saving as FormArray;
  }
  get floan() {
    return this.loanForm.controls;
  }
  get iloan() {
    return this.floan.loan as FormArray;
  }

  goBack() {

 
      this.router.navigate([
        `/loan/account/application-enrichment`,
      ]);
    
  }

  onSubmit(form) {}
  edit() {
    this.dialogRef.close();
    this.api.setFlag({
      flag: true,
    });
    this.router.navigate([
      `/loan/account/application-enrichment/intrest-details`,
    ]);
  }
  check(accountType) {
    if (accountType) {
      console.log(this.snack.open);
      this.snack.open("Intrest detail is empty", "Error", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    }
  }
  
}
