import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { STEP_STATE } from '@angular/cdk/stepper';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';


import { MatVerticalStepper } from '@angular/material/stepper';
import { AccountService } from '../../account.service';
@Component({
  selector: 'app-supervisor-approved-details',
  templateUrl: './supervisor-approved-details.component.html',
  styleUrls: ['./supervisor-approved-details.component.scss']
})
export class SupervisorApprovedDetailsComponent implements OnInit {
  
  isLinear = false;


  @ViewChild(MatVerticalStepper) stepper: MatVerticalStepper;
  loanInfo: any;
  ngAfterViewInit() {
    console.log('in afterview init')
    this.stepper._getIndicatorType = () =>  STEP_STATE.NUMBER; //'number'
  }
  data: any;
  customerInfoResp: any;
  loanEntrySummaryInfo: { isAppEntryView: boolean; data: any; };
  loanEnrichmentSummaryInfo: { isAppEntryView: boolean; data: any; };
  customerInfo: { isAppEntryView: boolean; data: any; };
  loanaccountID: any;



  constructor(
    public appEntryService: ApplicationEntryStageService, private ls: LocalStoreService,private apiService:ApiService,
    private router: Router,private accountService : AccountService, private dialogService:DialogService
    // private cdk: ChangeDetectorRef
    ) { }

  ngOnInit(): void {
    this.ls.setItem('isapprovedSupervisor', true);
    this.apiService.sendApprovedStatus(true);
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    console.log("this.loanaccountID", this.loanaccountID);
    setTimeout(() => {
      this.getLoanEntryStageInfo(this.loanaccountID);
    }, 10);
  
    //this.getLoanEnrichmentStageInfo(this.loanaccountID);
  }

  getLoanEntryStageInfo(loanaccountID) {
    this.appEntryService.getLoanEntryStageInfo(loanaccountID)
      .subscribe(loanResp => {
        this.data = loanResp;
        if (this.data != null) {
          console.log('data :: ', this.data);
        //  this.setData(loanResp)
          this.fetchCustomersByLoanAccIdForViewSummary(loanaccountID);
        }
      });
  }
  /*getLoanEnrichmentStageInfo(loanaccountID) {
    this.appEntryService.getLoanENtrichmentInfo(loanaccountID)
      .subscribe(loanResp => {
        this.data = loanResp;
        if (this.data != null) {
          console.log('data :: ', this.data);
          //this.setData1(loanResp)
          this.fetchCustomersByLoanAccIdForViewSummary(loanaccountID);
        }
      });
  }*/
  fetchCustomersByLoanAccIdForViewSummary(loanaccountID) {
    this.appEntryService.fetchCustomersByLoanAccIdForViewSummary(loanaccountID)
      .subscribe(custResp => {
        console.log('custResp :: ', custResp);
        this.customerInfoResp = custResp;
        if (this.customerInfoResp != null) {
          this.customerInfo = {
            'isAppEntryView': true,
            'data': this.customerInfoResp.data
          }
        }
        // this.cdk.markForCheck();
        // this.cdk.detectChanges();
      });
  }

  
  Done(){
    this.ls.setItem('APPROVED_DETAILS', true);
    this.ls.setItem('SUPERVISOR_APPROVAL_STAGE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/loan/account/offer-issue/list`]);
  }
  Back(){
    this.router.navigate([`/loan/account/supervisor-summary`]);
  }
    
  // getAllData(id) {
  //   this.apiService.getQualitativeScorecardDetails(id).subscribe((response) => {
  //     this.qualitativeScoreboardSummary = response;
  //   });

}