import { DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from '../../account.service';

@Component({
  selector: 'app-valuation-of-assets',
  templateUrl: './valuation-of-assets.component.html',
  styleUrls: ['./valuation-of-assets.component.scss']
})
export class ValuationOfAssetsComponent implements OnInit {
  
  maxDate = new Date();
  minDate = new Date();
  lastData:any;
  accountForm: FormGroup;
  isValuationofAsset: any;
  showEditOption: boolean = false;
  loanaccountID: any;
  loanInfo: any;  
  constructor(private formBuilder: FormBuilder,private ls: LocalStoreService,
    private snack: MatSnackBar,private loader: AppLoaderService,private api: ApiService, private apiService: ApiService,public datepipe: DatePipe, private router: Router,private accountService:AccountService) { 
      this.accountForm = this.formBuilder.group({
      bankValuation : [''],
      assetType: [''],
      propertyAreaSize: [''],
      mktValOfAsset: [''],
      assetValue: [''],
      valuationDate: [''],
      actualPropertyAreaSize: [''],
      actualPropertyAreaInUnits: [''],
      borrowersMktValOfAsset:[''],
      forcedSaleValue: [''],
      faceValOfAsset: [''],
     // propertyAreaInUnits: ['']
      })
     
      
    }

  ngOnInit(): void {
    /*this.isValuationofAsset = this.ls.getItem('VALUATION_OF_ASSET_DONE');
    if(this.isValuationofAsset == false){
      this.buildAccountForm();
    }
else{
  this.gettingValuationDetail();
}*/
//this.gettingValuationDetail();
this.loanaccountID = this.ls.getItem('loanAccountId');
this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
console.log(this.loanInfo);
if(this.loanInfo){
  this.loanaccountID = this.loanInfo.loanAccountId;
}
let isValuationofAsset: boolean = JSON.parse(this.ls.getItem('VALUATION_OF_ASSET_DONE'));
    //if (this.legalOpinionId != null && isValuationofAsset) {
      if (this.loanaccountID != null && isValuationofAsset) {
      this.showEditOption = true;
      setTimeout(() => {
        this.gettingValuationDetail();
      }, 10);
      
    }
    else {
      console.log("else");
      this.showEditOption = false;
      this.buildAccountForm();
    }
   

    this.lastUpdateByCif();
  }

  gettingValuationDetail() {
   /* this.api.gettingValuationDetails('200')
      .subscribe((resp: any) => {
       console.log(resp);
       this.buildAccountForm(resp,true);
      });*/
      this.loader.open();
    this.api.gettingValuationDetails(this.loanaccountID)
      .subscribe((resp: any) => {
        this.loader.close();
       console.log(resp);
       this.buildAccountForm(resp,true);
      }, (err: any) => {
        this.loader.close();
      });
  }

  buildAccountForm(data?,isResponse?:boolean) {
    console.log(data);
    let applicationDate = data ? moment(data.valuationDate).format() : '';
    //let valuation_date= new Date(data.valuationDate).toISOString().substring(0, 10);
    this.accountForm = this.formBuilder.group({
      actualPropertyAreaInUnits: [data? data.actualPropertyAreaInUnits:'',Validators.required],
      actualPropertyAreaSize:[data? data.actualPropertyAreaSize:'',Validators.required],
      assetType:[data? data.assetType:'',Validators.required],
      assetValue:[data? data.assetValue:'',Validators.required],
      bankValuation:[data? data.bankValuation:'',Validators.required],
      borrowersMktValOfAsset:[data? data.borrowersMktValOfAsset:'',Validators.required],
      forcedSaleValue:[data? data.forcedSaleValue:'',Validators.required],
      faceValOfAsset:[data? data.faceValOfAsset:'',Validators.required],
      loanAccountId:[ this.loanaccountID],
     // loanAccountId:[data?data.loanaccountID:''],
      mktValOfAsset:[data? data.mktValOfAsset:'',Validators.required],
     // propertyAreaInUnits:[data? data.propertyAreaInUnits:'',Validators.required],
      propertyAreaSize:[data? data.propertyAreaSize:'',Validators.required],
      valuationDate:[data? applicationDate:'',Validators.compose([ Validators.required, Validators.min(this.lastData)])],
      valuationId:[data? data.valuationId:'']
    });
    console.log("this.form",this.accountForm)
 
  }

  OnSubmit(val){
    console.log(val,"Values");
    val.valuationDate= moment(val.valuationDate).format('YYYY-MMM-DD HH:mm:ss');
    // this.ls.setItem('CREDIT_RATING_DONE', true);
    this.api.saveValuationDetails(val).subscribe((data: any) => {
      console.log(data);
      if (data === 200 || data !== null) {
        this.snack.open(' Valuation Assets Details Saved!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        //this.ls.setItem('VALUATION_OF_ASSET_DONE', true);
        this.next();
      }
      else if (data === 500) {
        this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    }, err => {
      console.log(err);
    });
  }
  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
next(){
  this.ls.setItem('VALUATION_OF_ASSET_DONE', true);
  this.accountService.refresh.next();
  this.router.navigate([`/loan/account/list/underwritingStage/legalopinion`]);

}
lastUpdateByCif(){
  this.apiService.getLoanDetails(this.loanaccountID).subscribe((resp:any)=>{
   
   this.lastData = this.datepipe.transform(resp.applicationDate,"yyyy-MM-dd");
 
  }, (err: any) => {
    console.error(err);
  })
}
goBack(){
   this.router.navigate([
      `/loan/account/list/underwritingStage/creditrating`,
    ]);
}

}
