import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-loan-repayment-summary',
  templateUrl: './loan-repayment-summary.component.html',
  styleUrls: ['./loan-repayment-summary.component.scss']
})
export class LoanRepaymentSummaryComponent implements OnInit {
  resp: any;
  loanAccountId: any;
  loanInfo: any;
  constructor(
    public ls: LocalStoreService,
    public dialogRef: MatDialogRef<LoanRepaymentSummaryComponent>, private api:ApiService,private router: Router) { }
  
  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem("loanAccountId");
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.api.loanRepaymentFetchById(this.loanAccountId).subscribe(resp=>{
        this.resp=resp;
        console.log(this.resp)
      }
        )
    }, 10);
    
    
  }
  goToClose(): void {
    this.dialogRef.close();
  }
  goToEdit() {
    this.dialogRef.close();
    this.router.navigate([`/loan/account/application-enrichment/loan-repayment/${this.loanAccountId}`]);

    // this.router.navigate([`/home/application/customer-information/${this.productAccountIdCust}`]);
  }
}