import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-offer-assessment',
  templateUrl: './offer-assessment.component.html',
  styleUrls: ['./offer-assessment.component.scss']
})
export class OfferAssessmentComponent implements OnInit {
  offerAssessmentFormGroup: FormGroup;
  disableDone:boolean =true;
  isAssesmentDetailsScreen: boolean = false;
  isOfferIssueScreen: boolean = false; 
  isOfferIssueRejectScreen: boolean  = false;
  arraySystemRecommendation:any[]=['Approved','Rejected'];
 
  
    
    
    constructor( private formBuilder: FormBuilder,
      private ls: LocalStoreService,
      private apiService: ApiService,
      private snack: MatSnackBar, 
      private router: Router) {
      this.offerAssessmentFormGroup = this.formBuilder.group({
        loanAccountId:['520',[Validators.required]],
        rateOfInterest : ['',[Validators.required]],
        requestedLoanAmount: ['',[Validators.required]],
        loanTenure: ['',[Validators.required]],
        approvedLoanAmount:['',[Validators.required]],
        userRecommendation:['',[Validators.required]],
        systemRecommendation:['',[Validators.required]],
        loanAmountRecommendation:['',[Validators.required]]
      })
     }


     
  ngOnInit(): void {
    
  }
  goBack(){
    this.router.navigate(["/loan/account/offerstage/list"]);
  }
  goNext(){
   
      this.apiService.updateAssessment(this.offerAssessmentFormGroup.value).subscribe(data => {
        console.log("data",data);
        if(data != null){
          this.snack.open('Data Saved!', 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          //this.cdr.markForCheck();
        }
      })
      
     
    
 
     
    
    this.ls.setItem('assessmentScreen', true);
    this.router.navigate(["/loan/account/list/offer-issue/offerissue"]);
  }
  
}
