import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-post-offer-amendment-stage-summary',
  templateUrl: './post-offer-amendment-stage-summary.component.html',
  styleUrls: ['./post-offer-amendment-stage-summary.component.scss']
})
export class PostOfferAmendmentStageSummaryComponent implements OnInit {
  offerDetailsList: any;
  offerDetails : any;
  loanAccountId: any;
  loanInfo: any;

  constructor(private router: Router,private apiService: ApiService, private ls: LocalStoreService ,private cdr:ChangeDetectorRef) { }

  ngOnInit(): void {
    this.cdr.markForCheck()
    this.loanAccountId=this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.getViewSummary();
    }, 10);
   
  }

  goBack() {
    this.router.navigate([`loan/account/post-offer-amend-stage/list`]);
  }

  done(){
    this.router.navigate([`/loan/account/account-creation-stage/`]);
  }

  getViewSummary(){
    this.apiService.getOfferAmendDetails(this.loanAccountId).subscribe(result=>
      {
        console.log("result",result);
        // this.offerDetailsList=result[3];
        this.offerDetails=result[3];
        console.log("result",result);
        // console.log("offerDetails::",this.offerDetailsList);
        console.log("offerDetails::",this.offerDetails);
      })
  }
}