import { DatePipe } from "@angular/common";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { AccountService } from "../../account.service";

@Component({
  selector: "app-account-services",
  templateUrl: "./account-services.component.html",
  styleUrls: ["./account-services.component.scss"],
})
export class AccountServicesComponent implements OnInit {
  maxDate = new Date();
  minDate = new Date();
  lastData:any;
  flag1: boolean=false;
  flag2: boolean=false;
  flag3: boolean=false;
  flag4: boolean=false;
  flag5: boolean=false;
  flag6: boolean=false;
  statementPreferencesForm: FormGroup;
  holidayPreferenceForm: FormGroup;
  maturityForm: FormGroup;
  revisionForm: FormGroup;
  loanaccountID: any;
  loanInfo: any; 
  constructor(
    private formBuilder: FormBuilder,
    public datepipe: DatePipe,
    public api: ApiService,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private router:Router,
    private ls: LocalStoreService,
    private accountService: AccountService,
  ) {}

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.buildAccountForm();
    this.lastUpdateByCif();
    setTimeout(() => {
      
      this.api.AccountServicesFetchById(this.loanaccountID).subscribe((resp) => {
        console.log("resp from backend ", resp);
        // if (resp) {
          this.buildStatmentPreferenceForm(resp);
          resp.serviceDetails.find((obj) => obj.serviceName === "Maturity");
          this.buildMaturityForm(
            resp.serviceDetails.find((obj) => obj.serviceName === "Maturity")
          );
          this.buildRevisionForm(
            resp.serviceDetails.find((obj) => obj.serviceName === "Revision")
          );
          this.buildHolidayForm(
            resp.serviceDetails.find(
              (obj) => obj.serviceName === "Holiday Preference"
            )
          );
        // }
      });
    }, 10);
   
  }

  buildAccountForm() {
    this.statementPreferencesForm = this.formBuilder.group({
      // requiredDisbursement: [true],
      statementCycle: ["", Validators.required],
      statementType: ["", Validators.required],
      statementGeneration:["",Validators.required],
      startDate: ["", Validators.compose([ Validators.required, Validators.min(this.lastData)])],
      accServiceId: [],
      loanAccountId: [this.loanaccountID],
      // firstDisburmentDate:['',Validators.required],
      // disbursementMode:['',Validators.required],
    });
    this.holidayPreferenceForm = this.formBuilder.group({
      ignoreHolidays: [true],
      holidayCheck: ["", Validators.required],
      moveAcrossMonth: ["", Validators.required],
      cascadeSchedules: ["", Validators.required],
      accServiceDetailsId: [""],
      accountServiceId: [""],
      serviceName: ["Holiday Preference"],
    });
    this.maturityForm = this.formBuilder.group({
      ignoreHolidays: [true],
      holidayCheck: ["", Validators.required],
      moveAcrossMonth: ["", Validators.required],
      cascadeSchedules: ["", Validators.required],
      accServiceDetailsId: [""],
      accountServiceId: [""],
      serviceName: ["Maturity"],
    });
    this.revisionForm = this.formBuilder.group({
      ignoreHolidays: [true],
      holidayCheck: ["", Validators.required],
      moveAcrossMonth: ["", Validators.required],
      cascadeSchedules: ["", Validators.required],
      accServiceDetailsId: [""],
      accountServiceId: [""],
      serviceName: ["Revision"],
    });
  }
  goBack() {
   this.router.navigate([
      `/loan/account/application-enrichment`,
    ]);
  }
  clickToggle1(){
    this.flag1=!this.flag1;
  }
  clickToggle2(){
    this.flag2=!this.flag2;
  }clickToggle3(){
    this.flag3=!this.flag3;
  }
  clickToggle4(){
    this.flag4=!this.flag4;
  }
   clickToggle5(){
    this.flag5=!this.flag5;
  }
   clickToggle6(){
    this.flag6=!this.flag6;
  }
  buildMaturityForm(item) {
    this.maturityForm = this.formBuilder.group({
      ignoreHolidays: [item ? item.ignoreHolidays : true],
      holidayCheck: [item ? item.holidayCheck : "", Validators.required],
      moveAcrossMonth: [item ? item.moveAcrossMonth : "", Validators.required],
      cascadeSchedules: [
        item ? item.cascadeSchedules : true,
        Validators.required,
      ],
      accServiceDetailsId: [item ? item.accServiceDetailsId : ""],
      accountServiceId: [item ? item.accountServiceId : ""],
      serviceName: ["Maturity"],
    });
  }
  buildRevisionForm(item) {
    this.revisionForm = this.formBuilder.group({
      ignoreHolidays: [item ? item.ignoreHolidays : true],
      holidayCheck: [item ? item.holidayCheck : "", Validators.required],
      moveAcrossMonth: [item ? item.moveAcrossMonth : "", Validators.required],
      cascadeSchedules: [
        item ? item.cascadeSchedules : true,
        Validators.required,
      ],
      accServiceDetailsId: [item ? item.accServiceDetailsId : ""],
      accountServiceId: [item ? item.accountServiceId : ""],
      serviceName: ["Revision"],
    });
  }
  buildHolidayForm(item) {
    this.holidayPreferenceForm = this.formBuilder.group({
      ignoreHolidays: [item ? item.ignoreHolidays : true],
      holidayCheck: [item ? item.holidayCheck : "", Validators.required],
      moveAcrossMonth: [item ? item.moveAcrossMonth : "", Validators.required],
      cascadeSchedules: [
        item ? item.cascadeSchedules : true,
        Validators.required,
      ],
      accServiceDetailsId: [item ? item.accServiceDetailsId : ""],
      accountServiceId: [item ? item.accountServiceId : ""],
      serviceName: ["Holiday Preference"],
    });
  }
  buildStatmentPreferenceForm(item) {
    let latest_date = new Date(item.startDate).toISOString().substring(0, 10);
    this.statementPreferencesForm = this.formBuilder.group({
      statementCycle: [item ? item.statementCycle : "", Validators.required],
      statementType: [item ? item.statementType : "", Validators.required],
      statementGeneration: [item ? item.statementGeneration : "", Validators.required],
      startDate: [item ? latest_date : "", Validators.required],
      accServiceId: [item ? item.accServiceId : ""],
      loanAccountId: [this.loanaccountID],
    });
  }
  submit() {
    let obj_test: any = {};
    obj_test.serviceDetails = [];
    let latest_date = this.datepipe.transform(
      this.statementPreferencesForm.value.startDate,
      "yyyy-MMM-dd"
    );
    obj_test.statementCycle = this.statementPreferencesForm.value.statementCycle;
    obj_test.statementType = this.statementPreferencesForm.value.statementType;
    obj_test.statementGeneration = this.statementPreferencesForm.value.statementGeneration;
    obj_test.startDate = latest_date;
    (obj_test.accServiceId = this.statementPreferencesForm.value.accServiceId),
      (obj_test.loanAccountId =
        this.statementPreferencesForm.value.loanAccountId),
      (obj_test.serviceDetails[0] = this.maturityForm.value);
    obj_test.serviceDetails[1] = this.revisionForm.value;
    obj_test.serviceDetails[2] = this.holidayPreferenceForm.value;
    console.log("obj_test ", obj_test);
    this.api.saveAccountService(obj_test).subscribe((resp) => {
      console.log("resp ", resp);
      this.snack.open("Account Services Saved!", "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.next();
    },
    (err) => {
      this.snack.open(
        "Not able to connect to server!",
        "INTERNAL SERVER ERROR",
        {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        }
      );
    });
  }
  lastUpdateByCif(){
    this.apiService.getLoanDetails(this.loanaccountID).subscribe((resp:any)=>{
     
     this.lastData = this.datepipe.transform(resp.applicationDate,"yyyy-MM-dd");
   
    }, (err: any) => {
      console.error(err);
    })
  }
  next(){
    this.ls.setItem('LOAN_ACCOUNT_SERVICES_DONE', true);
    this.ls.setItem('LOAN_ENRICHMENT_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([
      `/loan/account/list/underwritingStage`,
    ]);
  }
}
