import { Component, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';

@Component({
  selector: 'app-post-offer-amendment-summary',
  templateUrl: './post-offer-amendment-summary.component.html',
  styleUrls: ['./post-offer-amendment-summary.component.scss']
})
export class PostOfferAmendmentSummaryComponent implements OnInit {
  offerDetails: any;
  loanAccountId: any;
  loanInfo: any;

  constructor(public dialogRef: MatDialogRef<PostOfferAmendmentSummaryComponent>,private apiService: ApiService,
    private dataService:DataService, private ls: LocalStoreService,private router: Router,) { }

  ngOnInit(): void {
    this.loanAccountId = this.ls.getItem('loanAccountId');
    console.log(this.loanAccountId);
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanAccountId = this.loanInfo.loanAccountId;
    }
    setTimeout(() => {
      this.fetchPostOfferAmendmentDetails();
    }, 10);
   
  }

  fetchPostOfferAmendmentDetails() {
    this.apiService.getOfferAmendmentDetails( this.loanAccountId )
      .subscribe((data : any)=> {
        console.log(data);
        this.offerDetails = data;
        console.log("details",this.offerDetails);
      })
  }

edit(){
  this.dataService.sendNavParam(this.offerDetails);
  this.dialogRef.close();
  this.router.navigate(['loan/account/post-offer-amendment']);
  
}
close(){
  this.dialogRef.close();
  this.router.navigate([`/loan/account/post-offer-amend-stage/list`]);
}
}
