import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';
import { AccountService } from '../../account.service';
import { DatePipe } from '@angular/common'

@Component({
  selector: 'app-vehicle-details',
  templateUrl: './vehicle-details.component.html',
  styleUrls: ['./vehicle-details.component.scss'],
})
export class VehicleDetailsComponent implements OnInit {
  vehicleForm: FormGroup;
  vehicleDetails: any;
  showEditOption: boolean = false;
  loanaccountID: any;
  productName: any;
  arrayCity: string[];
  arrayState: string[];
  months: any[] = [];
  selectedYear: number;
  years: any[] = [];
  month: string[];
   year: string[];
   make:string[];
  policyNumber: any = true;
  loanInfo: any;

  constructor(
    private fb: FormBuilder,
    private ls: LocalStoreService,
    private loader: AppLoaderService,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private accountService: AccountService,
    private router: Router,
     public datepipe: DatePipe
  ) {}

  ngOnInit(): void {
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    console.log(this.loanInfo);
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.productName = this.ls.getItem('productName');

    this.selectedYear = new Date().getFullYear();
    for (let year = this.selectedYear; year >= 1990; year--) {
      this.years.push(year);
    }
    this.months = [
      '01',
      '02',
      '03',
      '04',
      '05',
      '06',
      '07',
      '08',
      '09',
      '10',
      '11',
      '12',
    ];
    let isVehicleDetailsScreen: boolean = JSON.parse(
      this.ls.getItem('VEHICLE_DETAIL_DONE')
    );
    //if (this.loanAccountId != null && isVehicleDetailsScreen) {
      setTimeout(() => {
        if (this.loanaccountID != null && isVehicleDetailsScreen) {
          this.showEditOption = true;
    
          this.getVehicleDetails();
        } else {
          console.log('else');
          this.showEditOption = false;
          this.buildformGroup();
        }
      }, 10);
  
 //   this.vehicleForm.get('productName').patchValue(this.productName);
    this.getStateName();
  }

  changeInput(inp) {
    this.policyNumber = inp.length ? false : true;
  }

  getVehicleDetails() {
    this.loader.open();
    //this.apiService.getAssetValuation(this.loacnAccountId).subscribe((accountDetails) => {
    this.apiService.getVehicleDetails(this.loanaccountID).subscribe(
      (vehicleDetails) => {
        this.loader.close();
        this.vehicleDetails = vehicleDetails;
        console.log(this.vehicleDetails);        
        this.buildformGroup(this.vehicleDetails);
      },
      (err: any) => {
        this.loader.close();
      }
    );
  }

  buildformGroup(data?) {
    this.getStateName();
    this.vehicleForm = this.fb.group({
      id: [''],
      insuranceDetails: [data ? data.insuranceDetails : ''],
      distanceRun: [data ? data.distanceRun : ''],
      engineNumber: [data ? data.engineNumber : ''],
      expectedSellingPrice: [data ? data.expectedSellingPrice : ''],
      insuranceCompany: [data ? data.insuranceCompany : ''],
      model: [data ? data.model : ''],
      policyCommencementDate: [data ? (this.datepipe.transform(data.policyCommencementDate, 'yyyy-MM-dd')) : '',[Validators.required]],
      policyNumber: [data ? data.policyNumber : ''],
      policyRenewalDate: [data ? (this.datepipe.transform(data.policyRenewalDate, 'yyyy-MM-dd')): '',[Validators.required]],
      premiunAmount: [data ? data.premiunAmount : ''],
      premiunFrequency: [data ? data.premiunFrequency : ''],
      productName: [data ? data.productName : this.productName, [Validators.required]],
      maker: [data ? data.maker : '', [Validators.required]],
      hypothecatedBranch: [data ? data.hypothecatedBranch : ''],
      registrationCity: [data ? data.registrationCity : ''],
      registrationNumber: [data ? data.registrationNumber : ''],
      registrationState: [data ? data.registrationState : ''],
      vehicleClass: [data ? data.vehicleClass : '', [Validators.required]],
      chassisNumber: [data ? data.chassisNumber : ''],
      make: [ data ? data.make : ''],
     month: [data ? data.make.split("-")[1] : "", [Validators.required]],
      year: [data ? data.make.split("-")[0] : "", [Validators.required]],
    });
  }
  goBack() {
    this.router.navigate([`/loan/account/loan-details`]);
  }

  getStateName() {
    let entityName = 'state';
    this.apiService.getDropDownValues(entityName).subscribe((data) => {
      this.arrayState = data.sort();
      console.log('-->', this.arrayState);
    });
  }
  getCityName(state: any) {
    console.log('inside getCityName::', state);
    let entityName = 'city';
    this.apiService.getDropDownValues1(entityName, state).subscribe((data) => {
      this.arrayCity = data;
      console.log('-->', this.arrayCity);
    });
  }

  next() {
    this.ls.setItem('VEHICLE_DETAIL_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate([`/loan/account/customer-information`]);
  }

  _keyPress(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }
   convertDate(value) {
    let date = moment(value).format('YYYY-MM-DD'); // ISO 8601 format
   // let date =  moment(value).format('yyyy-MMM-dd');
    console.log("date", date);
    if (date)
      return date;
  }
  submitForm() {
    var pk = this.vehicleForm.value.expectedSellingPrice;
    pk = parseInt(pk);
    var prAmmount = this.vehicleForm.value.premiunAmount;
    prAmmount = parseInt(prAmmount);
    const renewData =
    this.vehicleForm.value.year +
    "-" +
    this.vehicleForm.value.month ;
   
  this.vehicleForm.get("make").setValue(renewData);  
 // delete this.vehicleForm.value.month;  
    let payload = {
      loanAccountId: this.loanaccountID,
      insuranceDetails: this.vehicleForm.value.insuranceDetails,
      distanceRun: this.vehicleForm.value.distanceRun,
      engineNumber: this.vehicleForm.value.engineNumber,

      expectedSellingPrice: pk,
     // loanAmount: pk,
      insuranceCompany: this.vehicleForm.value.insuranceCompany,
      purposeOfLoan: this.vehicleForm.value.purposeOfLoan,
      model: this.vehicleForm.value.model,
     // policyCommencementDate:this.vehicleForm.value.policyCommencementDate==null? dateStringWithTime1 :null,
      policyNumber: this.vehicleForm.value.policyNumber,
     // policyRenewalDate:this.vehicleForm.value.policyRenewalDate==null?  dateStringWithTime2 :null,
      policyRenewalDate : this.datepipe.transform(this.vehicleForm.value.policyRenewalDate, 'yyyy-MMM-dd'),
      //policyCommencementDate:this.convertDate(this.vehicleForm.value.policyCommencementDate),
      policyCommencementDate : this.datepipe.transform(this.vehicleForm.value.policyCommencementDate, 'yyyy-MMM-dd'),
      premiunAmount: prAmmount,
      premiunFrequency: this.vehicleForm.value.premiunFrequency,
      productName: this.vehicleForm.value.productName,
      registrationCity: this.vehicleForm.value.registrationCity,
      registrationNumber: this.vehicleForm.value.registrationNumber,
      registrationState: this.vehicleForm.value.registrationState,
      vehicleClass: this.vehicleForm.value.vehicleClass,
      chassisNumber: this.vehicleForm.value.chassisNumber,
      maker:this.vehicleForm.value.maker,
      hypothecatedBranch: this.vehicleForm.value.hypothecatedBranch,
      make:renewData,
    };
    console.log('payload', payload);
    this.apiService.saveVehicleDetails(payload).subscribe((data) => {
      console.log('data', data);
      if (data != null) {
        this.snack.open('Vehicle Details saved Successfully', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
      }
      if (data === 500) {
        this.snack.open(
          'Not able to connect to server!',
          'INTERNAL SERVER ERROR',
          {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right',
          }
        );
        console.log('500 error');
      }
    });

    this.next();
  }

  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
}
