import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { rumangoAnimations } from 'app/shared/animations/rumango-animations';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AdmissionDetailsViewComponent } from './admission-details/admission-details-view/admission-details-view.component';
import { CollateralDetailsSummmaryComponent } from './collateral-details/collateral-details-summmary/collateral-details-summmary.component';
import { CustomerInfoViewComponent } from './customer-information/customer-info-view/customer-info-view.component';
import { FinancialDetailsViewComponent } from './financial-details/financial-details-view/financial-details-view.component';
import { GuarantorDetailsViewComponent } from './guarantor-details/guarantor-details-view/guarantor-details-view.component';
import { MandateDetailsViewComponent } from './mandate-details/mandate-details-view/mandate-details-view.component';

@Component({
  selector: 'app-loan-entry-stage',
  templateUrl: './loan-entry-stage.component.html',
  styleUrls: ['./loan-entry-stage.component.scss'],
  animations:rumangoAnimations
})
export class LoanEntryStageComponent implements OnInit {

  isLoanApplicationEntryDone :boolean=false;
  isLoanDetailScreen :boolean=false;
  isAssetsDetailScreen :boolean=false;
  isVehicleDetailScreen :boolean=false;
  isAdmissionDetailScreen :boolean=false;
  isFinancialDetailScreen :boolean=false;
  isMandateDetailScreen :boolean=false;
  isGuarantorDetailScreen :boolean=false;
  isCustomerInformationScreen:boolean=false;
  isCollateralDetailsScreen:boolean=false;
  isAnyScreen:boolean=false;


  isCustomerDetailScreen :boolean=false;

  loanaccountID : any;


  constructor(private router:Router,private dialogService : DialogService,private ls: LocalStoreService, public dialog: MatDialog) { }
  isHomeLoan: boolean=false;
  isVehicleLoan: boolean=false;
  isEducationLoan: boolean=false;

  ngOnInit(): void {
     //Parent 1
     this.isLoanApplicationEntryDone = this.ls.getItem('LoanEntryStage');
     //Parent 1 Child Nav
     this.isLoanDetailScreen = this.ls.getItem('LOAN_DETAIL_DONE');
     this.isAssetsDetailScreen = this.ls.getItem('ASSET_DETAIL_DONE');
     this.isVehicleDetailScreen = this.ls.getItem('VEHICLE_DETAIL_DONE');
     this.isAdmissionDetailScreen = this.ls.getItem('ADMISSION_DETAIL_DONE');
     this.isCustomerInformationScreen = this.ls.getItem('CUSTOMERINFO_DETAIL_DONE');
     this.isFinancialDetailScreen = this.ls.getItem('FINANCIAL_DETAIL_DONE');
     this.isMandateDetailScreen = this.ls.getItem('MANDATE_DETAIL_DONE');
     this.isGuarantorDetailScreen = this.ls.getItem('GUARANTOR_DETAIL_DONE');
     this.isCollateralDetailsScreen = this.ls.getItem('COLLATERAL_DETAIL_DONE');
     this.isCustomerDetailScreen = this.ls.getItem('CUSTOMERINFO_DETAIL_DONE');
     if(this.isAssetsDetailScreen==true || this.isVehicleDetailScreen ==true || this.isAdmissionDetailScreen==true){
       this.isAnyScreen=true;
     }

    

     if(this.ls.getItem('accountType')=="Home Loan"){
      this.isHomeLoan=true;
      this.isVehicleLoan=false;
      this.isEducationLoan=false;
     }
     else if (this.ls.getItem('accountType')=="Vehicle Loan"){
      this.isHomeLoan=false;
      this.isVehicleLoan=true;
      this.isEducationLoan=false;
     }
     else if(this.ls.getItem('accountType')=="Education Loan"){
      this.isHomeLoan=false;
      this.isVehicleLoan=false;
      this.isEducationLoan=true;
     }

  }

  goToAdmissionDetails() {
    if (this.isAdmissionDetailScreen) {
      const dialogRef = this.dialog.open(AdmissionDetailsViewComponent, { disableClose: true });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/admission-details`]);
    }
  }

  goToFinancialDetails() {
    if (this.isFinancialDetailScreen) {
      const dialogRef = this.dialog.open(FinancialDetailsViewComponent, { disableClose: true });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/financial-details`]);
    }
  }

  goToMandateDetails() {
    if (this.isMandateDetailScreen) {
      const dialogRef = this.dialog.open(MandateDetailsViewComponent, { disableClose: true });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/mandate-details`]);
    }
  }

  goToGuarantorDetails() {
    if (this.isMandateDetailScreen) {
      const dialogRef = this.dialog.open(GuarantorDetailsViewComponent, { disableClose: true });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/guarantor_details`]);
    }
  }

  goToCollateralDetails(){
    if (this.isCollateralDetailsScreen && this.isFinancialDetailScreen) {
      const dialogRef = this.dialog.open(CollateralDetailsSummmaryComponent, { disableClose: true });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/collateral_details`]);
    }
  }

  goToCustomerInfoDetails() {
    if (this.isCustomerDetailScreen) {
      const dialogRef = this.dialog.open(CustomerInfoViewComponent, { disableClose: true, maxHeight : '90%', width : '70%' });
      dialogRef.afterClosed().subscribe((submit) => {
      })
    }
    else {
      this.router.navigate([`/loan/account/customer-information`]);
    }
  }

  goToLoan(){
    this.router.navigate([`/loan/account/loan-details`]);
  }
  goToLoanSummary(){
   this.dialogService.loanDetailsSummaryDialog();
  }
  goToLoanEntry(){
    this.dialogService.assetDetailsSummaryDialog();
  }
  goToVehicleDetails(){
    this.dialogService.vehicleDetailsSummaryDialog();
  }

  openLoanEntryStageViewSummary(){
    this.dialogService.loanAppEntryStageViewSummary();
  }
}
