import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { ApiService } from 'app/shared/services/api.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as moment from 'moment';
import { AccountService } from '../../account.service';
import { ApplicationEntryStageService } from 'app/shared/services/application-entry-stage.service';

@Component({
  selector: 'app-account-create-details',
  templateUrl: './account-create-details.component.html',
  styleUrls: ['./account-create-details.component.scss']
})
export class AccountCreateDetailsComponent implements OnInit {
 
  maxDate = new Date();
  minDate = new Date();
  accountForm: FormGroup;
  isAccountCreateDetails: boolean;
  loanaccountID: any;
  loanInfo: any;
  loanTenure: any;
  selectedDay: number = 1;
  //day: number;
  days: any[] = [];
  months: any[] = [];
  selectedYear: number;
  //year: number;
  years: any[] = [];
  cifNumber: any;


  constructor(private formBuilder: FormBuilder, private router: Router, private ls: LocalStoreService,
    private appApiService: ApplicationEntryStageService,private dialogService : DialogService,private api: ApiService,private apiService:ApiService,
    private snack: MatSnackBar,private accountService:AccountService) { 
    this.accountForm= this.formBuilder.group({
      applicantName: ['',[Validators.required]],
      approvedLoanAmount:['',[Validators.required]],
      loanTenure:[''],
      installmentFrequency:[''],
      installmentType:[''],
      principal:[''],
      installmentAmount:[''],
      offerIssueDate:['',[Validators.required]],
      offerAcceptedDate:[''],
      accountCreateId:[''],
      rateOfInterest:[''],
      interest:[''],
      charges:[''],
      disbursementAmount:[''],
      repaymentAmount:[''],
      loanAccountId:[''],
      year:[''],
      month:[''],
      day:['']
      
    })
  }

  ngOnInit(): void {
    this.selectedYear = new Date().getFullYear();
 
    for (let year = 0; year <= 50; year++) {
      this.years.push(year+ " year");
    }
   
    for (let month = 0; month <= 11; month++) {
      this.months.push(month+ " month");
    }
   
    for (let day = 0; day <= 30; day++) {
      this.days.push(day+ " day");
    }
    this.loanaccountID = this.ls.getItem('loanAccountId');
    this.loanInfo = this.ls.getItem('LOAN_ACC_ARRAY');
    if(this.loanInfo){
      this.loanaccountID = this.loanInfo.loanAccountId;
    }
    this.isAccountCreateDetails = this .ls.getItem('ACCOUNT_CREATE_DETAILS_DONE');
    setTimeout(() => {
      if(this.isAccountCreateDetails==false){
        this.buildaccountForm();
      }
      else{
        
        this.getAccountCreateDetails();
      }
    }, 10);
    this.fetchByCif();
    this.loanData();
    this.intrestData();
    this.installmentTypeData();
    this.interestLoadData();
    this.dataLoad();
}
  getAccountCreateDetails() {
        this.api.getAccountCreateDetails(this.loanaccountID)
         .subscribe((resp: any) => {
           console.log(resp);
          
           this.buildaccountForm(resp,true);
      
           });
  }

  buildaccountForm(data?,Response?:boolean) {
    data.offerIssueDate = data ? moment(data.offerIssueDate).format() : '';
    data.offerAcceptedDate = data ? moment(data.offerAcceptedDate).format() : '';
    console.log(data);
    
    this.accountForm = this.formBuilder.group({
      accountCreateId:[''],
      loanAccountId:[data ? data.loanAccountId:''],
      applicantName: [data ? data.applicantName:''],
      approvedLoanAmount:[data ? data.approvedLoanAmount:''],
      loanTenure:[data ? data.loanTenure:''],
      installmentFrequency:[data ? data.installmentFrequency:''],
      principal:[data ? data.principal:''],
      installmentAmount:[data ? data.installmentAmount:''],
      disbursementAmount:[data ? data.disbursementAmount:''], 
      offerAcceptedDate:[data ? data.offerAcceptedDate:''],
      offerIssueDate:[data ? data.offerIssueDate:'',[Validators.required]],
      installmentType:[data ? data.installmentType:''],
      rateOfInterest:[data ? data.rateOfInterest:''],
      interest:[data ? data.interest:''],
      charges:[data ? data.charges:''],
      repaymentAmount:[data ? data.repaymentAmount:''],
      year:[data?data.loanTenure.split("-")[0]:''],
      month:[data?data.loanTenure.split("-")[1]:''],
      day:[data?data.loanTenure.split("-")[2]:'']
    });
    console.log("this.accountForm",this.accountForm);
}
goBack() {
  this.router.navigate(['loan/account/account-creation-stage']);
}

letterOnly(event) {
  var charCode = event.keyCode;
          if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
              return true;
          else
              return false;
}
_keyPress(event: any) {
  const pattern = /[0-9]/;
  let inputChar = String.fromCharCode(event.charCode);
  if (!pattern.test(inputChar)) {
    event.preventDefault();

  }
}

onSubmit(){
  let now = new Date();
  now=this.accountForm.value.offerIssueDate;
  var dateStringWithTime = moment(now).format('YYYY-MMM-DD HH:MM:SS');
  let now1 = new Date();
  now1=this.accountForm.value.offerAcceptedDate;
  var dateStringWithTime1 = moment(now).format('YYYY-MMM-DD HH:MM:SS');
  console.log("dateStringWithTime",dateStringWithTime);
  var aprloan=this.accountForm.value.approvedLoanAmount;
  aprloan= parseInt(aprloan);
  var rate=this.accountForm.value.rateOfInterest;
  rate= parseInt(rate);
    const renewData =
    this.accountForm.value.year +
    "-" +
    this.accountForm.value.month +
    "-" +
    this.accountForm.value.day;
  this.accountForm.get("loanTenure").setValue(renewData);
   this.accountForm.value.loanTenure=renewData;
  let payload={
    loanAccountId:this.loanaccountID,
    approvedLoanAmount:aprloan,
    applicantName:this.accountForm.value.applicantName,
    loanTenure:renewData,
    installmentType:this.accountForm.value.installmentType,
    installmentFrequency:this.accountForm.value.installmentFrequency,
    rateOfInterest:rate,
    principal:this.accountForm.value.principal,
    installmentAmount:this.accountForm.value.installmentAmount,
    interest:this.accountForm.value.interest,
    charges:this.accountForm.value.charges,
    disbursementAmount:this.accountForm.value.disbursementAmount,
    repaymentAmount:this.accountForm.value.repaymentAmount,
    offerIssueDate:dateStringWithTime,
    offerAcceptedDate:dateStringWithTime1
  }
  console.log("payload",payload)
  //val.offerIssueDate= moment(val.offerIssueDate).format('YYYY-MMM-DD HH:mm:ss');
  //val.offerAcceptedDate= moment(val.offerAcceptedDate).format('YYYY-MMM-DD HH:mm:ss');
 this.api.saveAccountCreateDetails(payload).subscribe((data: any) => {
  console.log(data);
  
  if (data === 200 || data !== null) {
    this.snack.open(' Account Create Details Saved!', 'OK', {
      duration: 4000,
      verticalPosition: 'top',
      horizontalPosition: 'right'
    });
    this.ls.setItem('ACCOUNT_CREATE_DETAILS_DONE', true);
    this.ls.setItem('ACCOUNT_CREATTION_STAGE_DONE', true);
    this.accountService.refresh.next();
    this.router.navigate(['loan/account/digitalsigning/list']);
  }
  // else if (data === 500) {
  //   this.snack.open('Not able to connect to server!', 'INTERNAL SERVER ERROR', {
  //     duration: 4000,
  //     verticalPosition: 'top',
  //     horizontalPosition: 'right'
  //   });
  // }
}, err => {
  console.log(err);
});
}

fetchByCif(){
  this.cifNumber=this.ls.getItem('cId');
  this.appApiService.fetchByCif(this.cifNumber).subscribe((res: any) => {
  this.accountForm.get('applicantName').setValue(res.customerInfoList[0].firstName +" "+ res.customerInfoList[0].lastName);
  }, (err: any) => {
    console.error(err);
  })
}
loanData(){
  console.log(this.loanaccountID);
  this.apiService.fetchByLoanAccountId(this.loanaccountID).subscribe((res:any)=>{   
    console.log(res); 
    this.accountForm.get('approvedLoanAmount').setValue(res.approvedLoanAmount); 
    console.log(res.loanTenure);       
    let dataYear = res.loanTenure.split("-")[0];
    console.log(dataYear);
    let dataMonth = res.loanTenure.split("-")[1];
    console.log(dataMonth);
    let dataDay = res.loanTenure.split("-")[2];
    console.log(dataDay);    
    this.accountForm.get('year').setValue(dataYear);
    this.accountForm.get('month').setValue(dataMonth);
    this.accountForm.get('day').setValue(dataDay);
  
  }, (err: any) => {
    console.error(err);
  })
}
intrestData(){
  console.log(this.loanaccountID);
  this.apiService.fetchLoanAssessment(this.loanaccountID).subscribe((res:any)=>{    
    this.accountForm.get('rateOfInterest').setValue(res.rateOfInterest);
  })
}
installmentTypeData(){
  console.log(this.loanaccountID);
  this.apiService.getOfferAcceptReject(this.loanaccountID).subscribe((res:any)=>{
    console.log(res);
     this.accountForm.get('installmentType').setValue(res.installmentType);
     this.accountForm.get('installmentFrequency').setValue(res.installmentFrequency);
     let issueDate =  moment(res.offerIssueDate).format();
     this.accountForm.get('offerIssueDate').setValue(issueDate);
     let acceptDate = moment(res.offerAcceptOrRejectDate).format();
     this.accountForm.get('offerAcceptedDate').setValue(acceptDate);
     console.log(issueDate);
  });
}
interestLoadData(){
  console.log(this.loanaccountID);
  this.apiService.getingOfferIssueDetails(this.loanaccountID).subscribe((res:any)=>{
    console.log(res);
     this.accountForm.get('interest').setValue(res.interest);    
     this.accountForm.get('charges').setValue(res.charges);
     this.accountForm.get('principal').setValue(res.principal);
     this.accountForm.get('installmentAmount').setValue(res.installmentAmount);
      
  });
}

dataLoad(){
  console.log(this.loanaccountID);
  this.apiService.loanDisbursementFetchById(this.loanaccountID).subscribe((res:any)=>{
    console.log(res);
    this.accountForm.get('disbursementAmount').setValue(res.totalDisbursement);
  })
}

}
