  closeDetails(): void {
    this.selectedApplicant = null;
    this.applicant.reset();
    this.applicantForms.get('numOfApplicants').enable({onlySelf:true});
    
  }

  onChange(event:number){
    this.reData = this.loanAssessmentForm.get('requestedLoanAmount').value;
    console.log(this.reData);
    
    this.loanData = this.loanAssessmentForm.get('loanAmountRecommendation').value;
    console.log(this.loanData);
    if(event == this.reData){
      this.isInvalid = true;
    }
     
  }

  toggleDetails(account) {
    console.log("customerId incomming", account);
    this.applicantForms.get('numOfApplicants').disable({onlySelf:true, emitEvent:false});
    let address: any;
    console.log(account.userAddress.length);
    if (account.userAddress.length > 1) {
      address = account.userAddress.find((item: any) => item.addressType == "Permanent");
    } else {
      address = account.userAddress[0];
    }
    // this.getStateName(address.country);
    // this.getCityName(address.state);

    let payLoadModel = {
      existingCustomer: account.existingCustomer,
      dependentId: account.dependentId,
      primaryCustomer: account.primaryCustomer,
      cifNumber: account.cifNumber,
      prefix: account.prefix,
      firstName: account.firstName,
      middleName: account.middleName,
      lastName: account.lastName,
      gender: account.gender,
      dateOfBirth: account.dateOfBirth,
      addressType: address.addressType,
      address1: address.address1,
      address2: address.address2,
      city: address.city,
      state: address.state,
      country: address.country,
      zipCode: address.zipCode,
      phoneNumber: account.phoneNumber,
      landlineNumber: account.landlineNumber,
      primaryEmailAdress: account.primaryEmailAdress
    };
    console.log("account.firstName=",account.firstName);
    this.selectedApplicant = account;
    this.applicant.patchValue(payLoadModel, {onlySelf:false, emitEvent:false});
    this.cdr.markForCheck();
  }

  getListOfAccounts() {
   // this.loader.open();
    this.appApiService.fetchCustomersByLoanAccId(this.loanaccountID,'loan')
      .subscribe((resp: any) => {
        this.console.log("resp:",resp)
        this.console.log("address1:",resp.data[0].userAddress[0].address1)
        this.address1=resp.data[0].userAddress[0].address1;
        this.address2=resp.data[0].userAddress[0].address2;
        this.city=resp.data[0].userAddress[0].city;
        this.state=resp.data[0].userAddress[0].state;
        this.country=resp.data[0].userAddress[0].country;
        this.zipCode=resp.data[0].userAddress[0].zipCode;
        this.listOfAccounts = resp.status == 200 ? resp.data.map(value => ({ ...value, _id: Math.round(Math.random() * 10000000000) })) : [];
        this.loader.close();
        console.log("All Customer Details", this.listOfAccounts.length);
        resp.status == 200 ? this.ls.setItem("cId", resp.data[0].customerId) : null;
        console.log("Length check??",this.listOfAccounts.length);

        if(this.listOfAccounts.length == 0){
          this.applicant.get('existingCustomer').patchValue(true);
          this.applicant.get('primaryCustomer').patchValue(true);
        }else if(this.listOfAccounts.length == 1){
          this.applicant.get('existingCustomer').patchValue(true);
          this.applicant.get('primaryCustomer').patchValue(false);
        }else if(this.listOfAccounts.length >= 2){
          this.applicantForms.get('ownership').patchValue('Join');
          for (let index = 0; index < resp.data.length; index++) {
            const element = resp.data[index];
            this.applicantForms.get('numOfApplicants').patchValue(resp.data[index].numOfApplicants);
          }
        }

        this.cdr.markForCheck();

        this.toggleChanges();
      }, (err: any) => {
        this.loader.close();
      });
  }

  toggleChanges() {
    if (this.applicant.get('existingCustomer').value == true) {
      this.applicant.get('cifNumber').enable();
      this.applicant.get('cifNumber').setValidators([Validators.required]);
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
    else {
      this.applicant.get('cifNumber').disable();
      this.applicant.get('cifNumber').clearValidators();
      this.applicant.get('cifNumber').updateValueAndValidity();
    }
  }

