  onInput(event: any, flag) {
    if (flag) {
      this.approvedLoanAmount = event.target.value;
    } else {
      this.approvedLoanAmount= event;
    }
  
    console.log("this.salary)::", this.approvedLoanAmount);
    let sal = this.approvedLoanAmount.toString().replace(/,/g, '').split(".");
    sal[0] = sal[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    let final = sal.join(".");
    console.log("final", final);
    this.loanAssessmentForm.controls.approvedLoanAmount.setValue(final);
  }
  
  keyPress1(event: any) {
    const pattern = /^[0-9]*\.?[0-9]*$/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    const input = event.target.value;
    if (input.length === 0 && event.which === 48) {
      event.preventDefault();
    }
  }

  
