import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-offer-issue',
  templateUrl: './offer-issue.component.html',
  styleUrls: ['./offer-issue.component.scss']
})
export class PostOfferIssueComponent implements OnInit {
  OfferIssueForm:FormGroup;

  constructor(private fb:FormBuilder,private router: Router) { }

  ngOnInit(): void {
    this.buildformGroup();
  }
  buildformGroup(){
    this.OfferIssueForm = this.fb.group({
      id:[''],
      applicant_name:[''],
      instalment_frequency:[''],
      offer_issue_date:[''],
      account_type:[''],
      principal:[''],
      loan_amount:[''],
      loan_tenure:[''],
    })
  }

  Done(){
    this.router.navigate([`/loan/account/offerstage/list`]);
  }
  
  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
}
