import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-supervisor-assessment-details',
  templateUrl: './supervisor-assessment-details.component.html',
  styleUrls: ['./supervisor-assessment-details.component.scss']
})
export class SupervisorAssessmentDetailsComponent implements OnInit {
  assessmentForm:FormGroup;
  constructor(private fb:FormBuilder) { }

  ngOnInit(): void {
    this.buildformGroup();
  }

  buildformGroup(){
    this.assessmentForm = this.fb.group({
      requestedLoanAmount:[''],
      rateofInterest:[''],
      userRecommendation:[''],
      approvedLoanAmount:[''],
      loanTenure:[''],
      systemRecommendation:[''],
      loanAmountRecommended:[''],
      finalLoanTenure:[''],
    })
  }

}
