import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DialogService } from 'app/shared/services/dialogs/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AccountService } from 'app/views/home/account.service';

@Component({
  selector: 'app-offer-issue-stage',
  templateUrl: './offer-issue-stage.component.html',
  styleUrls: ['./offer-issue-stage.component.scss']
})
export class OfferIssueStageComponent implements OnInit {
  isCreditRatingScreen: boolean = false;
  isValuationAssetScreen: boolean = false;
  islegalOpinionScreen: boolean = false;
  isAssesmentDetailsScreen:boolean = false;
  isOfferIssueScreen:boolean = false;

  constructor(
    private router: Router,private dialogService : DialogService,private route: ActivatedRoute,private ls: 
    LocalStoreService,private cdr:ChangeDetectorRef ,private accountService:AccountService
  ) { }

  ngOnInit() {
  
    let overdraftStatus = this.ls.getItem('overDraftByStatus') || null;
    console.log("fetch",overdraftStatus);
    
    this.isAssesmentDetailsScreen = this.ls.getItem('assessmentScreen');
    this.isOfferIssueScreen = this.ls.getItem('OfferIssue');
    this.islegalOpinionScreen= this.ls.getItem('legalScreen');
    this.isValuationAssetScreen = this.ls.getItem('valuationAsset');
    this.isCreditRatingScreen = this.ls.getItem('CreditRatingDetails');
   

    this.accountService.refresh.subscribe((res: any) => {
     
       
      this.islegalOpinionScreen= this.ls.getItem('legalScreen');
      this.isValuationAssetScreen = this.ls.getItem('valuationAsset');
      this.isAssesmentDetailsScreen = this.ls.getItem('assessmentScreen');
     this.isOfferIssueScreen = this.ls.getItem('OfferIssue');
     this.isCreditRatingScreen = this.ls.getItem('CreditRatingDetails');
     
      this.cdr.markForCheck();
    });
   
  }
  goToAccountDetails(){
    this.dialogService.assetValutionSummaryDialog();
  }
  goToLegalOpinion(){
    this.dialogService.legalopinionSummaryDialog();
  }
  goToAssesmentDetails(){
    this.dialogService.offeredAssessmentSummaryDialog();
  }
  goToOfferIssue(){
    this.dialogService.offerIssue1SummaryDialog();
  }
  goCreditDetails(){
    this.dialogService.CreditRatingDetailsDialog();
  }


}
